import React, { useState, useMemo } from 'react';
import {
    Box,
    Button,
    Container,
    Divider,
    FormControl,
    FormControlLabel,
    Grid,
    makeStyles,
    Paper,
    Radio,
    RadioGroup,
    TextField,
    Typography,
    withStyles
} from '@material-ui/core';

import Meta from '../components/Meta';
import bucket from '../assets/images/bucket.png';
import { Controller, FormProvider, useForm } from 'react-hook-form';
import { ORDER_REASON_ABSENCE_VACATION, ORDER_REASON_CARE_CASE, ORDER_REASON_DEATH, ORDER_REASON_MOVE, ORDER_TIME_IMMEDIATE, ORDER_TIME_LATER, ORDER_REASON_INSOLVENCY,
            CUSTOMER_TYPE_PRIVATE, CUSTOMER_TYPE_BUSINESS, ORDER_INFO } from '../constants/key.constant';
import {COLOR_PRIMARY, COLOR_PRIMARY_DARK, COLOR_WHITE,} from '../constants/color.constant';
import InsolvencyDialog from '../components/dialog/InsolvencyDialog';
import OrderDateDiffErrorDialog from '../components/dialog/OrderDateDiffErrorDialog';
import NoRedirectionDateDialog from '../components/dialog/NoRedirectionDateDialog';
import NoStartDateDialog from '../components/dialog/NoStartDateDialog';
import CarecaseDialog from '../components/dialog/CarecaseDialog';
import { getDaysBetweenDates, getXAfterDate } from '../utils';

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    boxContainer: {
        position: 'relative',
        ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
        padding: '24px 10%',
        height: '100%',
        [theme.breakpoints.down('xs')]: {
          padding: '24px 5%',
        },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
    bucketWrapper: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    bucket: {
        width: '100%',
        height: '100%',
    },
    modal: {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        boxShadow: 24,
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #000',
        padding: 16,

    },
    form: {
      '& > *': {
        marginBottom: 16,
      },
    },
}));

const PrimaryRadio = withStyles({
  root: {
    color: COLOR_PRIMARY_DARK,
    '&$checked': {
      color: COLOR_PRIMARY_DARK,
    },
  },
  checked: {},
})((props) => <Radio color="default" {...props} />);

const LandingScreen = ({ history }) => {
    const classes = useStyles();
    const methods = useForm({ shouldUnregister: true });
    const { handleSubmit, control, setValue } = methods;

    let customerTypeDefault = CUSTOMER_TYPE_PRIVATE;
    let orderReasonDefault = ORDER_REASON_MOVE;
    let orderTimeDefault = ORDER_TIME_IMMEDIATE;
    let laterStartTimeDefault = getXAfterDate(new Date(), 8);
    let deliverAgainDateDefault = '';

    // Local Storage Data
    let localData = localStorage.getItem(ORDER_INFO);
    console.log('load ', localData);
    if (localData) {
        localData = JSON.parse(localData);
        if (localData.customer_type) {
            customerTypeDefault = localData.customer_type;
        }
        if (localData.order_reason) {
            orderReasonDefault = localData.order_reason; 
        }
        if (localData.order_time) {
            orderTimeDefault = localData.order_time; 
        }
        if (localData.later_start_time) {
            laterStartTimeDefault = localData.later_start_time;
        }
        if (localData.deliver_again_date) {
            deliverAgainDateDefault = localData.deliver_again_date;
        }
    };

    // States
    const [openInsolvencyModal, setOpenInsolvencyModal] = useState(false);
    const [openDateDiffModal, setDateDiffModal] = useState(false);
    const [openNoRedirectionDateModal, setNoRedirectionDateModal] = useState(false);
    const [openNoStartDateModal, setNoStartDateModal] = useState(false);
    const [openCarecaseModal, setCarecaseModal] = useState(false);
    const [stateLaterStartTime, setLaterStartTime] = useState(getXAfterDate(new Date(), 8));
    const [stateDeliverAgainDate, setDeliverAgainDate] = useState('');
    const [deliverAgainDateShow, setDeliverAgainDateShow] = useState(localData && localData.deliver_again_date && localData.order_reason === ORDER_REASON_ABSENCE_VACATION ? true :false);
    const [laterStartTimeShow, setLaterStartTimeShow] = useState(localData && localData.later_start_time && localData.order_time === ORDER_TIME_LATER ? true : false);
    const privateReasons = useMemo(() => [ 
        { key: ORDER_REASON_MOVE, label: 'Umzug'}, 
        { key: ORDER_REASON_ABSENCE_VACATION, label: 'Abwesenheit / Urlaub'}, 
        { key: ORDER_REASON_CARE_CASE, label: 'Betreuungsfall'}, 
        { key: ORDER_REASON_DEATH, label: 'Sterbefall'}], []);
    const businessReasons = useMemo(() => [ 
        { key: ORDER_REASON_MOVE, label: 'Umzug'}, 
        { key: ORDER_REASON_ABSENCE_VACATION, label: 'Abwesenheit / Urlaub'}, 
        { key: ORDER_REASON_INSOLVENCY, label: 'Insolvenz'}], []);
    const [reasons, setReasons] = useState(localData && localData.customer_type === CUSTOMER_TYPE_PRIVATE ? privateReasons : businessReasons);
    const orderTimes = useMemo(() => [ 
        { key: ORDER_TIME_IMMEDIATE, label: 'Sofort (5 Werktage Vorlauf)'}, 
        { key: ORDER_TIME_LATER, label: 'Späterer Startzeitpunkt'}], []);

    // Handler
    const handleOpenInsolvencyModal = () => setOpenInsolvencyModal(true);
    const handleCloseInsolvencyModal = () => setOpenInsolvencyModal(false);
    const handleOpenNoStartDateModal = () => setNoStartDateModal(true);
    const handleCloseNoStartDateModal = () => setNoStartDateModal(false);
    const handleOpenNoRedirectionDateModal = () => setNoRedirectionDateModal(true);
    const handleCloseNoRedirectionDateModal = () => setNoRedirectionDateModal(false);
    const handleOpenDateDiffModal = () => setDateDiffModal(true);
    const handleCloseDateDiffModal = () => setDateDiffModal(false);
    const handleOprnCareacaseModal = () => setCarecaseModal(true);
    const handleCloseCareacaseModal = () => {
        setCarecaseModal(false);
        history.push('/receiver');
    }
    const handleCustomerTypeChange = (v) => {
        if (v === CUSTOMER_TYPE_PRIVATE) {
            setReasons(privateReasons);
        } else {
            setReasons(businessReasons);
        }
        setValue('order_reason', ORDER_REASON_MOVE);
        setValue('order_time', ORDER_TIME_IMMEDIATE);
        setLaterStartTimeShow(false);
        setDeliverAgainDateShow(false);
    };
    const handleOrderReasonClick = (key) => {
        // setOrderReason(key);
        switch (key) {
            case ORDER_REASON_INSOLVENCY:
                setDeliverAgainDateShow(false);
                handleOpenInsolvencyModal();
                break;
            case ORDER_REASON_ABSENCE_VACATION:
                setDeliverAgainDateShow(true);
                break;
            default:
                setDeliverAgainDateShow(false);
                break;
        }
    }
    const handleOrderTimeClick = (key) => {
        // setOrderTime(key);
        switch (key) {
            case ORDER_TIME_IMMEDIATE:
                setLaterStartTimeShow(false);
                break;
            case ORDER_TIME_LATER:
                setLaterStartTimeShow(true);
                break;
            default:
                break;
        }
    };

    const onNext = (data) => {
        const { order_time, order_reason, later_start_time, deliver_again_date } = data;
        let isModal = false;
        console.log(data);
        // Validation

        if (order_time === ORDER_TIME_LATER) {
            if (later_start_time.length === 0) {
                // Require min date message
                handleOpenNoStartDateModal();
                return;
            }
            // if (getDaysBetweenDates(new Date(laterStartTime), new Date()) < 8) {
            //     // Require 8 days
            // }
        }
        if (order_reason === ORDER_REASON_ABSENCE_VACATION) {
            if (deliver_again_date.length === 0) {
                handleOpenNoRedirectionDateModal();
                return;
                // Require min date message
            }
            if (deliver_again_date && deliver_again_date.length > 0 && later_start_time && later_start_time.length > 0) {
                if (getDaysBetweenDates(new Date(deliver_again_date), later_start_time.length > 0 ? new Date(later_start_time) : new Date()) < 14) {
                    // Require 14 days
                    setLaterStartTime(stateLaterStartTime);
                    setDeliverAgainDate(stateDeliverAgainDate);
                    handleOpenDateDiffModal();
                    return;
                }
            }
        }
        if (order_reason === ORDER_REASON_CARE_CASE) {
            handleOprnCareacaseModal();
            isModal = true;
        }
        
        if (!data.later_start_time) {
            data.later_start_time = getXAfterDate(new Date(), 8);
        }
        localStorage.setItem(ORDER_INFO, JSON.stringify(data));

        if (!isModal) {
            history.push('/receiver');
        }
    }


    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}></Grid>
            <Box className={classes.boxContainer}>
                <Paper elevation={0} className={classes.content} square>
                    <Grid container spacing={8}>
                        <Grid item sm={12} md={4} className={classes.bucketWrapper}>
                            <Box>
                                <img src={bucket} alt='' className={classes.bucket} />
                            </Box>
                        </Grid>
                        <Grid item sm={12} md={8}>
                            <Typography variant='h5'  gutterBottom>
                                Post Nachsendeauftrag
                            </Typography>
                            <Typography variant='body1' gutterBottom>
                                Art &amp; Startzeitpunkt des Nachsendeauftrag:
                            </Typography>
                            <Divider style={{ marginTop:16, marginBottom: 16 }} />
                            
                            <FormProvider {...methods}>
                                <form className={classes.form} onSubmit={handleSubmit(onNext)}>
                                    <Grid container>
                                        <Grid item sm={12} md={6}>
                                            <Controller 
                                                name='customer_type'
                                                control={control}
                                                defaultValue={customerTypeDefault}
                                                render={({ field: {value, onChange}, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        {/* <InputLabel shrink htmlFor='form-customerType'>
                                                            {t('CorporateForm')}
                                                        </InputLabel> */}
                                                        <Grid container spacing={2} style={{ marginTop: '8px', marginBottom: '-16px' }}>
                                                            <RadioGroup aria-label='customer_type' id='form-customerType' style={{ flexDirection:"row" }} onChange={(e, v) => { handleCustomerTypeChange(v); onChange(e, v); }} value={value}>
                                                            <Grid item xs={12}>
                                                                <FormControlLabel value={CUSTOMER_TYPE_PRIVATE} control={<PrimaryRadio />} label='Privater Nachsendeauftrag' />
                                                            </Grid>
                                                            <Grid item xs={12}>
                                                                <FormControlLabel value={CUSTOMER_TYPE_BUSINESS} control={<PrimaryRadio />} label='Gewerblicher Nachsendeauftrag' />
                                                            </Grid>
                                                            </RadioGroup>
                                                        </Grid>
                                                    </FormControl>
                                                )}/>
                                        </Grid>

                                        <Grid item sm={12} md={6}>
                                            <Controller 
                                                name='order_reason'
                                                control={control}
                                                defaultValue={orderReasonDefault}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <Grid container spacing={2} style={{ marginTop: '8px', marginBottom: '-16px' }}>
                                                            <Box display={{ xs: 'block', sm: 'none', md: 'none'}}>
                                                                <Divider style={{ marginTop:16, marginBottom: 16 }} />
                                                            </Box>
                                                            <RadioGroup aria-label='order_reason' id='form-orderReason' style={{ flexDirection:"row" }} onChange={(e, v) => {handleOrderReasonClick(v); onChange(e, v)}} value={value}>
                                                                {reasons.map((reason, i) => (
                                                                    <Grid item xs={12} key={i}>
                                                                        <FormControlLabel value={reason.key} control={<PrimaryRadio />} label={reason.label} />
                                                                    </Grid>
                                                                ))}
                                                            </RadioGroup>
                                                        </Grid>
                                                    </FormControl>
                                                )}/>
                                        </Grid>
                                        
                                        <Grid item xs={12} md={12} sm={12}>
                                            <Divider style={{ marginTop: 32, marginBottom: 16 }} />
                                        </Grid>

                                        <Grid item sm={12} md={6}>
                                            <Controller 
                                                name='order_time'
                                                control={control}
                                                defaultValue={orderTimeDefault}
                                                render={({ field: {value, onChange}, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <Grid container spacing={2} style={{ marginTop: '8px', marginBottom: '8px' }}>
                                                            <RadioGroup aria-label='order_time' id='form-orderTime' style={{ flexDirection:"row" }} onChange={(e, v) => {handleOrderTimeClick(v); onChange(e, v); }} value={value}>
                                                                {orderTimes.map((time, id) => (
                                                                    <Grid item xs={12} key={id}>
                                                                        <FormControlLabel value={time.key} control={<PrimaryRadio />} label={time.label} />
                                                                    </Grid>
                                                                ))}
                                                            </RadioGroup>
                                                        </Grid>
                                                        {/* <Grid container spacing={2}>
                                                            { laterStartTimeShow && <Grid item xs={12}>
                                                            <Typography variant='subtitle1'>Späterer Startzeitpunkt:</Typography>
                                                                <TextField
                                                                    id="date-later-start-time"
                                                                    type="date"
                                                                    value={laterStartTime}
                                                                    onChange={e => setLaterStartTime(e.target.value)}
                                                                    InputLabelProps={{
                                                                        shrink: true,
                                                                    }}
                                                                    InputProps={{
                                                                        inputProps: {
                                                                            min: getXAfterDate(new Date(), 8)
                                                                        }
                                                                    }}
                                                                    style={{
                                                                        paddingLeft: 8,
                                                                        paddingRight: 8
                                                                    }}
                                                                />
                                                            </Grid> }
                                                            
                                                            { deliverAgainDateShow && <Grid item xs={12}>
                                                                <Typography variant='subtitle1'>Wieder zustellen ab</Typography>
                                                                <TextField
                                                                    id="date-deliver-again"
                                                                    type="date"
                                                                    value={deliverAgainDate}
                                                                    onChange={e => setDeliverAgainDate(e.target.value)}
                                                                    InputLabelProps={{
                                                                        shrink: true,
                                                                    }}
                                                                    InputProps={{
                                                                        inputProps: {
                                                                            min: getXAfterDate(orderTime === ORDER_TIME_LATER ? new Date(laterStartTime) : new Date(), 14)
                                                                        }
                                                                    }}
                                                                    style={{
                                                                        paddingLeft: 8,
                                                                        paddingRight: 8
                                                                    }}
                                                                />
                                                            </Grid> }
                                                            
                                                        </Grid> */}
                                                    </FormControl>
                                                )}/>

                                            <Grid container spacing={2}>
                                                {laterStartTimeShow && <Grid item xs={12}>
                                                    <Controller
                                                        name='later_start_time'
                                                        control={control}
                                                        defaultValue={laterStartTimeDefault}
                                                        render={({ field, fieldState: { error } }) => (
                                                            <FormControl error={!!error}>
                                                                <Typography variant='subtitle1'>Späterer Startzeitpunkt:</Typography>
                                                                <TextField
                                                                    {...field}
                                                                    id="date-later-start-time"
                                                                    type="date"
                                                                    InputLabelProps={{
                                                                        shrink: true,
                                                                    }}
                                                                    InputProps={{
                                                                        inputProps: {
                                                                            min: getXAfterDate(new Date(), 8),
                                                                            max: getXAfterDate(new Date(), 8, 1)
                                                                        }
                                                                    }}
                                                                    style={{
                                                                        paddingLeft: 8,
                                                                        paddingRight: 8
                                                                    }}
                                                                />
                                                            </FormControl>
                                                        )}/>
                                                </Grid> }
                                                
                                                { deliverAgainDateShow && <Grid item xs={12}>
                                                    <Controller
                                                        name='deliver_again_date'
                                                        control={control}
                                                        defaultValue={deliverAgainDateDefault}
                                                        render={({ field, fieldState: { error } }) => (
                                                            <FormControl error={!!error}>
                                                                <Typography variant='subtitle1'>Wieder zustellen ab</Typography>
                                                                <TextField
                                                                    {...field}
                                                                    id="date-deliver-again"
                                                                    type="date"
                                                                    InputLabelProps={{
                                                                        shrink: true,
                                                                    }}
                                                                    InputProps={{
                                                                        inputProps: {
                                                                            min: getXAfterDate(new Date(stateLaterStartTime), 14),
                                                                            max: getXAfterDate(new Date(stateLaterStartTime), 14, 1),
                                                                        }
                                                                    }}
                                                                    style={{
                                                                        paddingLeft: 8,
                                                                        paddingRight: 8
                                                                    }}
                                                                />
                                                            </FormControl>
                                                        )}/>
                                                </Grid> }
                                                
                                            </Grid>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                        <Box display='flex' justifyContent='center' mt={3}>
                                            <Button 
                                                type='submit'
                                                    variant='contained' 
                                                style={{
                                                    color: COLOR_WHITE,
                                                    backgroundColor: COLOR_PRIMARY_DARK
                                                }}>
                                                Jetzt beantragen
                                            </Button>
                                        </Box>
                                        </Grid>
                                    </Grid>
                                </form>
                            </FormProvider>
                        </Grid>

                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom>
                                Nachsendungen-online.de ist unabhängiger Dienstleister für Ihren Nachsendeauftrag und richtet diesen bei einer Vielzahl von Post-Zustellern, wie <span style={{ fontWeight: 'bold' }}>Deutsche Post, PostModern uvm</span>. ein.
                            </Typography> <br/>
                            <Typography variant='body1' gutterBottom>
                                Verpassen Sie keine Post mehr. Nutzen Sie unseren Service und wählen bis zu <span style={{ fontWeight: 'bold' }}>16 verschiedene Versanddienstleister</span> aus.
                            </Typography>
                        </Grid>
                    </Grid>
                </Paper>
            </Box>
            <InsolvencyDialog 
                openModal={openInsolvencyModal}
                handleCloseModal={handleCloseInsolvencyModal} />
            <NoStartDateDialog 
                openModal={openNoStartDateModal}
                handleCloseModal={handleCloseNoStartDateModal} />
            <NoRedirectionDateDialog 
                openModal={openNoRedirectionDateModal}
                handleCloseModal={handleCloseNoRedirectionDateModal} />
            <OrderDateDiffErrorDialog 
                openModal={openDateDiffModal}
                handleCloseModal={handleCloseDateDiffModal}
                deliverAgainDate={stateDeliverAgainDate}
                laterStartTime={stateLaterStartTime} />
            <CarecaseDialog
                openModal={openCarecaseModal}
                handleCloseModal={handleCloseCareacaseModal} />
        </Container>
    );
};

export default LandingScreen;