import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import LandingScreen from './screens/LandingScreen';
import ReceiverScreen from './screens/ReceiverScreen';

import Footer from './components/layout/Footer';
import Header from './components/layout/Header';
import AddressScreen from './screens/AddressScreen';
import SummaryScreen from './screens/SummaryScreen';
import PaymentScreen from './screens/PaymentScreen';
import PaymentSuccessScreen from "./screens/PaymentSuccessScreen";
import CheckoutScreen from "./screens/CheckoutScreen";
import ImprintScreen from "./screens/ImprintScreen";
import DataPrivacyScreen from "./screens/DataPrivacyScreen";
import RecallScreen from "./screens/RecallScreen";
import AgbScreen from "./screens/AgbScreen";
import ScrollToTop from "react-scroll-to-top";
import {COLOR_PRIMARY_DARK} from "./constants/color.constant";
import PaypalScreen from "./screens/PaypalScreen";

const App = () => {
    return (
        <Router>
            <Switch>
                <Route>
                    <ScrollToTop smooth color={COLOR_PRIMARY_DARK} style={{ marginBottom: 32, marginRight: 16 }}/>
                    <Header />
                    <main className='main'>
                        <Route path='/' component={LandingScreen} exact />
                        <Route path='/receiver' component={ReceiverScreen} />
                        <Route path='/address' component={AddressScreen} />
                        <Route path='/summary' component={SummaryScreen} />
                        <Route path='/payment' component={PaymentScreen} exact />
                        <Route path='/checkout' component={CheckoutScreen} />
                        <Route path='/checkout-paypal' component={PaypalScreen} />
                        <Route path='/payment/:service/:invoice' component={PaymentSuccessScreen} />
                        <Route path='/imprint' component={ImprintScreen} />
                        <Route path='/data-privacy' component={DataPrivacyScreen} />
                        <Route path='/agb' component={AgbScreen} />
                        <Route path='/recall' component={RecallScreen} />
                    </main>
                    <Footer />
                </Route>
            </Switch>
        </Router>
    );
};

export default App;