import React, {useEffect, useRef} from 'react';
import {PayPalScriptProvider, PayPalButtons, usePayPalScriptReducer} from '@paypal/react-paypal-js';
import Loader from "../Loader";
import {ORDER_INFO} from "../../constants/key.constant";
import {useHistory} from "react-router-dom";

const PaypalButtonsPanel = () => {
    const history = useHistory();
    const [{ isPending }] = usePayPalScriptReducer();
    let localData = localStorage.getItem(ORDER_INFO);
    if (!localData) {
        console.log('local data error');
    } else {
        localData = JSON.parse(localData);
    }

    return (
        <>
            { isPending && <Loader /> }
            <PayPalButtons
                createOrder={(data, actions) => {
                    // Create Invoice
                    // Create Order
                    // return actions.order.create({
                    //     purchase_units: [
                    //         {
                    //             amount: {
                    //                 value: "1.99",
                    //             },
                    //         },
                    //     ],
                    // });
                    console.log('request ', localData);
                    return fetch("/api/create-order", {
                      method: "post",
                        headers: {
                          'Content-Type': 'application/json',
                        },
                        body: JSON.stringify(localData)
                      // use the "body" param to optionally pass additional order information
                      // like product ids or amount
                    })
                      .then((response) => response.json())
                      .then((order) => { console.log('order :: ', order); return order.id; });
                }}
                onApprove={(data, actions) => {
                    return actions.order.capture().then((details) => {
                        const name = details.payer.name.given_name;
                        // Add Order to DB
                        // Finish Invoice
                        // Send Invoice Email
                        console.log('order detail body : ', details);
                        // return history.push('/payment/paypal/' + details['purchase_units'][0]['invoice_id']);
                        return history.push('/payment/paypal/' + details['id']);
                    });
                }}
                onCancel={() => {
                    console.log('Paypal cancelled');
                }}
                onError={(err) => {
                    console.log('Paypal checkout onError : ', err);
                }}
                onClick={(data, actions) => {
                    const hasAlreadyBought = false;
                    if (hasAlreadyBought) {
                        console.log('Already bought');
                    } else {
                        return actions.resolve();
                    }
                }}
                style={{
                    color: 'silver',
                    layout: 'vertical',
                    height: 48,
                    tagline: false,
                    shape: 'pill'
                }}
            />
        </>
    );
}

export default function CheckoutForm({ client_id, client_token }) {
    const initialOptions = {
        "client-id": client_id,
        currency: "EUR",
        "data-client-token": client_token,
    };

    return (
        <PayPalScriptProvider options={initialOptions}>
            <PaypalButtonsPanel />
        </PayPalScriptProvider>
    );
}