import React, {useEffect} from 'react';
import {Box, Button, Container, Grid, makeStyles, Paper, Typography} from '@material-ui/core';

import Meta from '../components/Meta';
import {deleteAllCookies, shootFireworks} from "../utils";
import {useDispatch, useSelector} from "react-redux";
import {finalizeInvoice} from "../actions/order.action";
import {COLOR_PRIMARY_DARK, COLOR_WHITE} from "../constants/color.constant";
const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    boxContainer: {
        position: 'relative',
        ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
        padding: '24px 10%',
        height: '100%',
        [theme.breakpoints.down('xs')]: {
          padding: '24px 5%',
        },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
}));

const PaymentSuccessScreen = ({ location, history }) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const paths = location.pathname.split('/');
    const invoiceId = paths.pop();
    const service = paths.pop();
    const { loading, error, isSuccess, invoiceData } = useSelector((state) => state.invoice);

    console.log(paths, service, invoiceId)
    deleteAllCookies();

    useEffect(() => {
        if (invoiceId && invoiceId.length > 0) {
            shootFireworks();
            dispatch(finalizeInvoice({
                service,
                invoiceId
            }))
        } else {
            history.push('/');
        }
    }, [invoiceId, dispatch, history]);

    useEffect(() => {
        if (isSuccess && invoiceData) {
            console.log(invoiceData);
        }
    }, [isSuccess, invoiceData]);

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}></Grid>
            <Box className={classes.boxContainer}>
                <Paper elevation={0} className={classes.content} square>
                    <Grid container spacing={8}>
                        <Grid item xs={12}>
                            <Box display='flex' justifyContent='center'>
                                <Typography variant='h4'>Vielen Dank für Ihre Bestellung</Typography>
                            </Box>
                        </Grid>
                        <Grid item xs={12}>
                            <Box display='flex' justifyContent='center'>
                                <Typography variant='h6'>Ihre Bestellung wurde erfolgreich aufgegeben.</Typography>
                            </Box>
                            <Box display='flex' justifyContent='center'>
                                <Typography variant='h6'>Bitte überprüfen Sie Ihren Posteingang für die Quittung.</Typography>
                            </Box>
                        </Grid>

                        <Grid item xs={12}>
                            <Box display='flex' justifyContent='flex-end'>
                                <Button
                                    variant='contained'
                                    style={{
                                        color: COLOR_WHITE,
                                        backgroundColor: COLOR_PRIMARY_DARK
                                    }}
                                    onClick={() => { localStorage.clear(); history.push('/') }}>Weiter zur Bestellung</Button>
                            </Box>
                        </Grid>
                    </Grid>
                </Paper>
            </Box>
        </Container>
    );
};

export default PaymentSuccessScreen;