import React from 'react';
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Typography } from '@material-ui/core';
import { getDaysBetweenDates, getXAfterDate } from '../../utils';

const OrderDateDiffErrorDialog = ({ openModal, handleCloseModal, deliverAgainDate, laterStartTime }) => {
    const diffDays = getDaysBetweenDates(new Date(deliverAgainDate), new Date(laterStartTime));
    const isBefore = diffDays <= 0;

    return (
        <Dialog
            open={openModal}
            onClose={handleCloseModal}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"Bitte korrigieren Sie Ihre Eingabe:"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description" component={'div'}>
                    {isBefore ? (
                        <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                            {`Ihr gewähltes Stardatum (${laterStartTime}) muss hinter dem gewählten Enddatum (${deliverAgainDate}) liegen. Bitte beachten Sie auch, dass das Startdatum auch bei sofortiger Beantragung eine Vorlaufzeit von 5 Werktagen hat.`}
                        </Typography>
                    ) :
                    (
                        <>
                            <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                                {`Die Dauer des Nachsendeauftrag muss mindestens 2 Wochen betragen. Das frühste mögliche Datum der Wiederzustellung ist der ${getXAfterDate(new Date(laterStartTime), 14)}`}
                            </Typography>
                            <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                                Möglicherweise suchen Sie eher nach einem Lagerauftrag.
                            </Typography>
                            <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                                Hierbei wird Ihre Post kurzzeitig gelagert und nach Ihrer Abwesenheit wieder an die alte Adresse zugestellt. Sie können diesen hier beauftragen
                            </Typography>
                        </>
                    )
                    }
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCloseModal} style={{ margin: 4 }} autoFocus>
                    Schliessen
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default OrderDateDiffErrorDialog;