import React, {useEffect} from 'react';
import {Box, Container, Grid, makeStyles, Paper, Typography} from '@material-ui/core';
import Meta from '../components/Meta';
import CheckoutForm from "../components/Paypal/CheckoutForm";
import {useSelector} from "react-redux";
import {ORDER_INFO} from "../constants/key.constant";
import Loader from "../components/Loader";

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    boxContainer: {
        position: 'relative',
        ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
        padding: '24px 10%',
        height: '100%',
        [theme.breakpoints.down('xs')]: {
          padding: '24px 5%',
        },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
}));

const PaypalScreen = ({ history }) => {
    const classes = useStyles();
    const { loading, error, isSuccess, paypalClientData } = useSelector((state) => state.paypalClient);

    let localData = localStorage.getItem(ORDER_INFO);
    if (!localData) {
        history.push('/');
    } else {
        localData = JSON.parse(localData);
    }

    useEffect(() => {
        if (isSuccess && paypalClientData) {
            console.log('payload : ', paypalClientData);
        }
    }, [
        isSuccess, paypalClientData
    ]);

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}></Grid>
            <Box className={classes.boxContainer}>
                { loading &&
                    <>
                        <Loader />
                        <Typography variant='h6'>Laden von Zahlungsinformationen...</Typography>
                    </>
                }
                { paypalClientData && paypalClientData.client_id &&
                    <Paper elevation={0} className={classes.content} square>
                        <Grid container spacing={8}>
                            <Grid item xs={12} style={{minWidth: 512}}>
                                <CheckoutForm client_id={paypalClientData.client_id} client_token={paypalClientData.client_token} />
                            </Grid>
                        </Grid>
                    </Paper>
                }
            </Box>
        </Container>
    );
};

export default PaypalScreen;