import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Typography } from '@material-ui/core';
import React from 'react';


const CarecaseDialog = ({ openModal, handleCloseModal }) => {
    return (
        <Dialog
            open={openModal}
            onClose={handleCloseModal}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"Betreuungsfall - Bitte beachten"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description" component={'div'}>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Bei einem Betreuungsfall benötigen wir nach Beantragung Ihren Betreuerausweis und Ihre Ausweiskopie oder eine amtliche Bescheinigung, um die Beantragung durchzuführen.
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Nach Bestellabschluss erhalten Sie Ihre Auftragsbestätigung per E-Mail. Dort finden Sie einen Link zum Upload Ihrer Dokumente. Bitten schicken Sie uns diese ausschließlich über den Upload-Link. Ein Versand mittels E-Mail oder Fax kann nicht bearbeitet werden.
                    </Typography>
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCloseModal} style={{ margin: 4 }} autoFocus>
                    Schliessen
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default CarecaseDialog;