import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, List, ListItem, ListItemIcon, ListItemText, Typography } from '@material-ui/core';
import React from 'react';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';

const wrapListItem = (props) => {
    return (
        <ListItem>
            <ListItemIcon>
                <ArrowRightIcon />
            </ListItemIcon>
            <ListItemText
                primary={props}
            />
        </ListItem>
    )
}

const wrapWithBold = (props) => {
    return <span style={{fontWeight: "bold"}}>
        {props}
    </span>
}

const InfopostDialog = ({ openModal, handleCloseModal }) => {
    return (
        <Dialog
            open={openModal}
            onClose={handleCloseModal}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"DHL Infopost / Dialogpost (schwer)"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description" component={'div'}>
                    <Typography variant='h6' style={{ marginTop: 8 }}>
                        Bedingungen für den Versand von Produkten via DHL Infopost / Dialogpost (schwer):
                    </Typography>
                    <Typography variant='subtitle1' style={{ marginTop: 8 }} component='p'>
                        Es werden ausschließlich größere Mengen an inhaltsgleichen Sendungen versendet.
                    </Typography>
                    <Typography variant='subtitle1' style={{ marginTop: 8 }} component='p'>
                        {'DHL Infopost / Dialogpost unter 1.000 g werden generell nachgesendet.'}
                    </Typography>
                    <Typography variant='subtitle1' style={{ marginTop: 8 }} component='p'>
                        {'Diese Option bezieht sich nur auf Sendungen schwerer 1.000 g.'}
                    </Typography>
                    <Typography variant='h5' style={{ marginTop: 8 }}>
                        Beispiele:
                    </Typography>
                    <List dense>
                        {wrapListItem("Produktproben")}
                        {wrapListItem("Kataloge")}
                        {wrapListItem("Zeitschriften, Broschüren und Zeitungen")}
                        {wrapListItem("Werbeartikel")}
                    </List> <br />
                    <Typography variant='subtitle1' style={{ marginTop: 8 }} component='p'>
                        Folgende Sendungen sind {wrapWithBold("keine DHL Infopost / Dialogpost")}:
                    </Typography>
                    <List dense>
                        {wrapListItem("Briefe mit weniger als 50 Adressaten")}
                        {wrapListItem("Mahnungen oder Rechnungen")}
                        {wrapListItem("Verkaufswaren (sofern nicht oben aufgeführt)")}
                    </List> <br />
                    <Typography variant='subtitle1' style={{ marginTop: 8 }} component='p'>
                        Das Nachsenden von DHL Infopost / Dalogpost (schwer) ist {wrapWithBold("kostenlos")} und wird von uns {wrapWithBold("generell empfohlen")}.
                    </Typography>
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCloseModal} style={{ margin: 4 }} autoFocus>
                    Schliessen
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default InfopostDialog;