import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
    Container,
    Link,
    makeStyles,
    Typography
} from '@material-ui/core';
import { 
    COLOR_WHITE,
    COLOR_GRAY_1
} from '../../constants/color.constant';

const useStyles = makeStyles((theme) => ({
    root: {
        borderTop: '1px solid rgba(0, 0, 0, 0.1)',
        backgroundColor: COLOR_WHITE,
        width: '100%',
        maxWidth: '100%',
        paddingTop: 16,
        paddingBottom: 16
    },
    box: {
        ...theme.mixins.customize.flexMixin('center', 'center'),
        color: COLOR_GRAY_1,
        '@media (max-width: 960px)': {
            textAlign: 'center',
            flexDirection: 'column',
            '& $copyright': {
                padding: '10px 0 0',
            }
        }
    },
    copyright: {
        flexGrow: 1,
        paddingLeft: 100
    },
    btnGroup: {
        ...theme.mixins.customize.flexMixin('center', 'center'),
    }
}));

const Footer = () => {
    const classes = useStyles();

    return (
        <footer>
            <Container className={classes.root}>
                <div className={classes.box}>
                    <Typography
                        variant='body2'
                        className={classes.copyright}
                        component='p'>
                        Copyright 2022 meinnachsendeservice.de
                    </Typography>
                    <div>
                        <Link
                            color='textPrimary'
                            style={{
                                paddingRight: 24,
                                paddingLeft: 24,
                                fontSize: 16
                            }}
                            component={RouterLink}
                            to={`/imprint`}>
                            Impressum
                        </Link>
                        <Link
                            color='textPrimary'
                            style={{
                                paddingRight: 24,
                                paddingLeft: 24,
                                fontSize: 16
                            }}
                            component={RouterLink}
                            to={`/data-privacy`}>
                            Datenschutz
                        </Link>
                        <Link
                            color='textPrimary'
                            style={{
                                paddingRight: 24,
                                paddingLeft: 24,
                                fontSize: 16
                            }}
                            component={RouterLink}
                            to={`/agb`}>
                            AGB
                        </Link>
                        <Link
                            color='textPrimary'
                            style={{
                                paddingRight: 24,
                                paddingLeft: 24,
                                fontSize: 16
                            }}
                            component={RouterLink}
                            to={`/recall`}>
                            Wiederruf
                        </Link>
                    </div>
                </div>
            </Container>
        </footer>
    );
};

export default Footer;