import React from 'react';
import { CircularProgress } from '@material-ui/core';
import { COLOR_PRIMARY_DARK } from '../constants/color.constant';

const Loader = ({ my }) => {
  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'center',
        width: '100%',
        marginTop: my,
        marginBottom: my,
      }}
    >
      <CircularProgress style={{color:COLOR_PRIMARY_DARK}} />
    </div>
  );
};

Loader.defaultProps = {
  my: 40,
};

export default Loader;
