import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Typography } from '@material-ui/core';
import React from 'react';


const NoParcelOrInfopostForOverseasDialog = ({ openModal, handleCloseModal, country }) => {
    return (
        <Dialog
            open={openModal}
            onClose={handleCloseModal}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"Bitte korrigieren Sie Ihre Eingabe:"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description" component={'div'}>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                    {`Bei einem internationalen Nachsendeauftrag (${country}) ist die Nachsendung von Paketen und Päckchen, sowie von DHL INFOPOST nicht möglich. Die Optionen wurden automatisch aus Ihrem Auftrag entfernt. Sie können nun mit der Beantragung fortfahren.`}
                    </Typography>
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCloseModal} style={{ margin: 4 }} autoFocus>
                    Schliessen
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default NoParcelOrInfopostForOverseasDialog;