import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Typography } from '@material-ui/core';
import React from 'react';

const wrapWithBold = (props) => {
    return <span style={{fontWeight: "bold"}}>
        {props}
    </span>
}

const ParcelsDialog = ({ openModal, handleAcceptModal, handleRejectModal }) => {
    return (
        <Dialog
            open={openModal}
            onClose={handleRejectModal}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"Zusatzentgelt für Päckchen und Pakete"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description" component={'div'}>
                    <Typography variant='h6' gutterBottom>
                        Hinweise:
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Die {wrapWithBold("Zusatzleistung")} "Päckchen und Pakete" erzeugt ein Zusatzendgelt, zusätzlich zum regulären Endpreis.
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Dieses {wrapWithBold("Zusatzentgelt")} wird direkt bei der Zustellung eines Paketes oder Päckchens an die neue Adresse vom Zusteller erhoben.
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Die Möglichkeit der Nachsendung von Paketen und Päckchen besteht {wrapWithBold("nur innerhalb Deutschlands")}.
                    </Typography>
                    <Typography variant='h6' gutterBottom>
                        Zusatzentgelt (Paketnachsendung):
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Der Preis für eine Paketnachsendung via Deutschen Post DHL beträgt pauschal 6,99 EUR (Stand: 17.06.2015).
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Während der Dauer eines Auftrags werden Presiänderungen zur Paketnachsendung seitens der Deutschen Post DHL  automatisch an die Kosten für Ihre Paketnachsendung angepasst.
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Die Kosten werden jeweils pro nachgesandter
                        Zustellung eines Pakets oder Päckchens an Ihre neue Anschrift erhoben. Es
                        handelt sich hierbei um die Kosten pro Sendung. Bei zwei erhaltenen
                        Pakten zahlen Sie den Aufschlag somit auch zweimal etc.
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Wenn Sie damit einverstanden sind und diese Belehrung
                         zur Kenntnis genommen haben klicken Sie bitte auf "Zustimmen".
                        Andernfalls auf "Ablehnen".
                    </Typography>
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleRejectModal} style={{ margin: 4 }} color='secondary'>
                    Ablehnen
                </Button>
                <Button onClick={handleAcceptModal} style={{ margin: 4 }} color='primary' autoFocus>
                    Zustimmen
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default ParcelsDialog;