import React from 'react';
import {
    AppBar,
    Link,
    makeStyles,
    Toolbar
} from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';
import logo from '../../assets/images/logo.png'
import { COLOR_WHITE } from '../../constants/color.constant';

const useStyles = makeStyles((theme) => ({
    header: {
        backgroundColor: COLOR_WHITE,
        transition: 'all .2s',
        boxShadow: '0px 2px 8px -1px rgb(0 0 0 / 10%)',
        paddingRight: '0 !important',
        paddingTop: '8px',
        paddingBottom: '8px',
    },
    logoContainer: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
    logoWrapper: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '30%',
        [theme.breakpoints.down('lg')]: {
          width: '50%',
        },
        [theme.breakpoints.down('sm')]: {
          width: '100%',
        },
    },
    logo: {
        flexGrow: 1,
        maxWidth: '100%',
    }
}));

const Header = () => {
    const classes = useStyles();

    return (
        <AppBar
            position='fixed'
            className={classes.header}>
            <Toolbar className={classes.logoContainer}>
                <Link to='/' className={classes.logoWrapper} component={RouterLink}>
                    <img src={logo} alt='Nachsenden.info' className={classes.logo} />
                </Link>
            </Toolbar>
        </AppBar>
    );
}

export default Header;