export const getCookie = (name) => {
    var cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        var cookies = document.cookie.split(';');
        for (var i = 0; i < cookies.length; i++) {
            var cookie = cookies[i].trim();
            if (cookie.substring(0, name.length + 1) === (name + '=')) {
                cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                break;
            }
        }
    }
    return cookieValue;
};

export const deleteAllCookies = () => {
    var cookies = document.cookie.split(";");

    for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i];
        var eqPos = cookie.indexOf("=");
        var name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
        document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
    }
}

export const getProductDetails = (data, paymentMethod) => {
    console.log(data.customerType);
    console.log(paymentMethod);
    console.log(data.otherService);
    let product = {
        customer: data.customerType,
        other_service: data.otherService,
        payment_method: paymentMethod
    };
    const serviceProduct = OTHER_SERVICES.find(service => service.name === data.otherService);
    console.log(serviceProduct);
    if (serviceProduct) {
        if (data.customerType === CUSTOMER_TYPE_PRIVATE) {
            if (paymentMethod === PAYMENT_6_MONTHS) {
                product.price = PRICE_PRIVATE_6_MONTHS + get(serviceProduct, 'private', 0);
                product.url = get(serviceProduct,'private_6_url', PRIVATE_6_BASEURL);
            } else {
                product.price = PRICE_PRIVATE_12_MONTHS + get(serviceProduct, 'private', 0);
                product.url = get(serviceProduct,'private_12_url', PRIVATE_12_BASEURL);
            }
        } else {
            if (paymentMethod === PAYMENT_6_MONTHS) {
                product.price = PRICE_BUSINESS_6_MONTHS + get(serviceProduct, 'business', 0);
                product.url = get(serviceProduct, 'business_6_url', BUSINESS_6_BASEURL);
            } else {
                product.price = PRICE_BUSINESS_12_MONTHS + get(serviceProduct, 'business', 0);
                product.url = get(serviceProduct, 'business_12_url', BUSINESS_12_BASEURL);
            }
        }
    } else {
        if (data.customerType === CUSTOMER_TYPE_PRIVATE) {
            if (paymentMethod === PAYMENT_6_MONTHS) {
                product.price = PRICE_PRIVATE_6_MONTHS;
                product.url = PRIVATE_6_BASEURL;
            } else {
                product.price = PRICE_PRIVATE_12_MONTHS;
                product.url = PRIVATE_12_BASEURL;
            }
        } else {
            if (paymentMethod === PAYMENT_6_MONTHS) {
                product.price = PRICE_BUSINESS_6_MONTHS;
                product.url = BUSINESS_6_BASEURL;
            } else {
                product.price = PRICE_BUSINESS_12_MONTHS;
                product.url = BUSINESS_12_BASEURL;
            }
        }
    }
    return product;
}

export const get = (object, key, default_value) => {
    var result = object[key];
    return (typeof result !== "undefined") ? result : default_value;
}

export const shootFireworks = () => {
  const duration = 15 * 1000;
  const animationEnd = Date.now() + duration;
  const defaults = { startVelocity: 30, spread: 360, ticks: 60, zIndex: 0 };

  function randomInRange(min, max) {
    return Math.random() * (max - min) + min;
  }

  const interval = setInterval(function () {
    const timeLeft = animationEnd - Date.now();

    if (timeLeft <= 0) {
      return clearInterval(interval);
    }

    const particleCount = 50 * (timeLeft / duration);
    // since particles fall down, start a bit higher than random
    confetti(
      Object.assign({}, defaults, {
        particleCount,
        origin: { x: randomInRange(0.2, 0.4), y: Math.random() - 0.2 },
      })
    );
    confetti(
      Object.assign({}, defaults, {
        particleCount,
        origin: { x: randomInRange(0.6, 0.8), y: Math.random() - 0.2 },
      })
    );
  }, 250);
};
