import React, { useState } from 'react';
import { Box, Breadcrumbs, Button, Checkbox, Container, Divider, FormControl, FormControlLabel, FormLabel, Grid, InputLabel, List, ListItem, ListItemIcon, ListItemText, makeStyles, Paper, Radio, RadioGroup, Typography, withStyles } from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';

import Meta from '../components/Meta';
import OrderSteps from '../components/OrderSteps';
import {
    CUSTOMER_TYPE_BUSINESS,
    ORDER_INFO,
    PAYMENT_12_MONTHS,
    PAYMENT_6_MONTHS,
    PAYMENT_SERVICE_PAYPAL,
    PRICE_BUSINESS_12_MONTHS,
    PRICE_BUSINESS_6_MONTHS,
    PRICE_PRIVATE_12_MONTHS,
    PRICE_PRIVATE_6_MONTHS
} from '../constants/key.constant';
import { COLOR_PRIMARY_DARK, COLOR_WHITE, COLOR_PRIMARY } from '../constants/color.constant';
import { Controller, FormProvider, useForm } from 'react-hook-form';
import HelpPanel from '../components/HelpPanel';
import {formatter, getCustomerType} from '../utils';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import { useDispatch } from 'react-redux';
// import {createOrder, createPayment} from '../actions/order.action';
// import Loader from '../components/Loader';
import currency from 'currency.js';
import {createOrder, initPaypal} from "../actions/order.action";
import PaymentGateway from "../components/PaymentGateway";
// import KlarnaPanel from '../components/KlarnaPanel';

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    form: {
      '& > *': {
        marginBottom: 16,
      },
    },
    boxContainer: {
      position: 'relative',
      ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
      padding: '24px 10%',
      height: '100%',
      [theme.breakpoints.down('xs')]: {
        padding: '24px',
      },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
}));

const PrimaryCheckBox = withStyles({
  root: {
    color: COLOR_PRIMARY,
    '&$checked': {
      color: COLOR_PRIMARY_DARK,
    },
  },
  checked: {},
})((props) => <Checkbox color="default" {...props} />);

const PrimaryRadio = withStyles({
  root: {
    color: COLOR_PRIMARY_DARK,
    '&$checked': {
      color: COLOR_PRIMARY_DARK,
    },
  },
  checked: {},
})((props) => <Radio color="default" {...props} />);


const PaymentScreen = ({ history }) => {
    const classes = useStyles();
    const methods = useForm({ shouldUnregister: true });
    const dispatch = useDispatch();
    const { handleSubmit, control } = methods;
    const [ paymentMethod, setPaymentMethod ] = useState(PAYMENT_6_MONTHS);
    const [ paymentService, setPaymentService ] = useState(PAYMENT_SERVICE_PAYPAL);
    const [focused, setFocused] = useState('');
    // const { loading, error, isSuccess, paymentSession } = useSelector((state) => state.payment);

    let localData = localStorage.getItem(ORDER_INFO);
    let price6Month = PRICE_PRIVATE_6_MONTHS;
    let price12Month = PRICE_PRIVATE_12_MONTHS;

    if (!localData) {
      history.push('/');
    } else {
      localData = JSON.parse(localData);
      if (localData.customer_type === CUSTOMER_TYPE_BUSINESS) {
        price6Month = PRICE_BUSINESS_6_MONTHS;
        price12Month = PRICE_BUSINESS_12_MONTHS;
      }
    }
    price6Month += localData.other_price;
    price12Month += localData.other_price;

    // const [ paymentPrice, setPaymentPrice ] = useState(price6Month);
    // const [ paymentPricePerMonth, setPaymentPricePerMonth ] = useState(currency(price6Month).divide(6));

    // useEffect(() => {
    //     console.log('------------------');
    //     console.log(loading, error, isSuccess, paymentSession);
    //     if (isSuccess && paymentSession) {
    //
    //     }
    // }, [loading, error, isSuccess, paymentSession]);

    const onNext = (data) => {
        localData = { ...localData, ...data};
        localData.payment_service = paymentService;
        localData.product_name = `${localData.customer_type} Zahlung - ${paymentMethod} Monat`;
        localData.product_price = paymentMethod === PAYMENT_6_MONTHS ? price6Month: price12Month;
        localData.base_price = localData.product_price - localData.other_price;
        localData.base_price = Math.round(localData.base_price * 100) / 100;
        // localData.product_price = localData.product_price.toFixed(2);
        localData.delivery_status = 'offen';
        console.log(localData);
        localStorage.setItem(ORDER_INFO, JSON.stringify(localData));
        // localData.product = getProductDetails(localData, paymentMethod);
        if (paymentService === PAYMENT_SERVICE_PAYPAL) {
            dispatch(initPaypal());
            history.push('/checkout-paypal')
        } else {
            dispatch(createOrder(JSON.stringify(localData)));
            history.push('/checkout');
        }
    };

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}>
                <Grid item xs={12}>
                    <Breadcrumbs style={{ marginBottom: 24 }}/>
                    <OrderSteps step={4} />
                </Grid>
            </Grid>
            <Box className={classes.boxContainer}>
                <Paper elevation={0} className={classes.content} square>
                    <Grid container spacing={8}>
                      <Grid item sm={12} md={7}>
                        <FormProvider {...methods}>
                        <form className={classes.form} onSubmit={handleSubmit(onNext)}>
                          <Grid container spacing={1}>
                            <Grid item xs={12}>
                              <Typography variant='h5' gutterBottom>
                                  {'Ihre Auftragszusammenfassung'}
                              </Typography>
                            </Grid>

                            <Grid item xs={12}>
                              <Controller
                                  name='paymentMethod'
                                  control={control}
                                  defaultValue={paymentMethod}
                                  render={({ field, fieldState: { error } }) => (
                                      <FormControl fullWidth error={!!error}>
                                          <InputLabel shrink htmlFor='form-paymentMethod'>
                                              {'Nachsendedauer'}
                                          </InputLabel>
                                          <Grid container spacing={2} style={{ marginLeft: '16px', marginTop: '32px', marginBottom: '16px' }}>
                                              {/* <Grid item xs={12} sm={6}> */}
                                                  <RadioGroup {...field} aria-label='paymentMethod' id='form-paymentMethod' style={{ flexDirection:"row", width: '100%'  }} onFocus={() => {setFocused('paymentType')}} onChange={(e, v) => { setPaymentMethod(e.target.value); field.onChange(e, v);}}>
                                                    <Grid item xs={12} sm={6}>
                                                      <FormControlLabel value={PAYMENT_6_MONTHS} control={<PrimaryRadio />} label={<label><span>6 Monate</span><br /><span>Preis: {formatter.format(currency(price6Month).divide(6))} pro Monat</span></label>} />
                                                    </Grid>
                                                    <Grid item xs={12} sm={6}>
                                                      <FormControlLabel value={PAYMENT_12_MONTHS} control={<PrimaryRadio />} label={<label><span>12 Monate</span><br /><span>Preis: {formatter.format(currency(price12Month).divide(12))} pro Monat</span></label>} />
                                                    </Grid>
                                                  </RadioGroup>
                                              {/* </Grid> */}
                                          </Grid>
                                      </FormControl>
                                  )}/>
                            </Grid>
                            <Grid item xs={12}>
                                <Divider style={{marginTop: 2, marginBottom: 4}} />
                            </Grid>

                            <Grid item xs={12}>
                              <Typography variant='h6' gutterBottom>
                                  {'Auftragsdaten'}
                              </Typography>
                              <Grid container spacing={1} style={{marginLeft: 16}}>
                                <Grid item xs={12}>
                                    <Typography variant='subtitle1' >
                                        {'Beantragung Nachsendeauftrag+'}
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography variant='subtitle1' >
                                        {`Art: ${localData ? getCustomerType(localData.customer_type) : ''} `}&nbsp;&nbsp;<span style={{color: COLOR_PRIMARY_DARK}}>{`${formatter.format(paymentMethod === PAYMENT_6_MONTHS ? price6Month: price12Month)}`}</span>
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                  <List dense>
                                    <ListItem>
                                        <ListItemIcon>
                                            <ArrowRightIcon />
                                        </ListItemIcon>
                                        <ListItemText
                                            primary={`Laufzeit: ${paymentMethod === PAYMENT_6_MONTHS ? '6' : '12'} Monate`}
                                        />
                                    </ListItem>
                                    <ListItem>
                                        <ListItemIcon>
                                            <ArrowRightIcon />
                                        </ListItemIcon>
                                        <ListItemText
                                            primary={`Nachsendung Deutsche Post AG`}
                                        />
                                    </ListItem>
                                    <ListItem>
                                        <ListItemIcon>
                                            <ArrowRightIcon />
                                        </ListItemIcon>
                                        <ListItemText
                                            primary={`Nachsendung Deutsche Post DHL (nur bei Paketoption)`}
                                        />
                                    </ListItem>
                                    <ListItem>
                                        <ListItemIcon>
                                            <ArrowRightIcon />
                                        </ListItemIcon>
                                        <ListItemText
                                            primary={`Adressprüfung und Profi-Check`}
                                        />
                                    </ListItem>
                                    <ListItem>
                                        <ListItemIcon>
                                            <ArrowRightIcon />
                                        </ListItemIcon>
                                        <ListItemText
                                            primary={`Weitere Versanddienstleister (nur bei Auswahl)`}
                                        />
                                    </ListItem>
                                    <ListItem>
                                        <ListItemIcon>
                                            <ArrowRightIcon />
                                        </ListItemIcon>
                                        <ListItemText
                                            primary={`kostenloser E-Mail- und Telefon-Support für gesamte Laufzeit`}
                                        />
                                    </ListItem>
                                </List>
                                </Grid>
                                {/* <Grid item xs={12}>
                                    <Typography variant='subtitle1' >
                                        {'Gesamtsumme: 89,00 EUR (inkl. 14,21 EUR MwSt. 19%).'}
                                    </Typography>
                                </Grid> */}
                              </Grid>
                            </Grid>

                            <Grid item xs={12}>
                                <Divider style={{marginTop: 2, marginBottom: 4}} />
                            </Grid>

                            <Grid item xs={12}>
                                <Typography variant='h6'>Zahlungsart auswählen</Typography>
                                <PaymentGateway
                                    paymentService={paymentService}
                                    setPaymentService={setPaymentService}
                                    setFocused={setFocused}
                                    />
                            </Grid>

                            <Grid item xs={12}>
                                <Divider style={{marginTop: 2, marginBottom: 4}} />
                            </Grid>

                            <Grid item xs={12}>
                              <Controller
                                  name='executionInfo'
                                  control={control}
                                  defaultValue={''}
                                  render={({ field, fieldState: { error } }) => (
                                      <FormControl fullWidth error={!!error} onFocus={() => setFocused('executionInfo')}>
                                          <FormLabel>Ausführungs-Info:</FormLabel>
                                          <FormControlLabel
                                              {...field}
                                              control={<PrimaryCheckBox />}
                                              style={{marginTop: 8}}
                                              label={'Ja, bitte beginnen Sie mit der Bearbeitung des Auftrag vor Ablauf der 14-tägigen Widerrufsfrist.'}
                                          />
                                      </FormControl>
                                  )}
                                rules={{
                                    required: 'Dieses Feld wird benötigt.',
                                }}
                                />
                            </Grid>

                            <Grid item xs={12}>
                              <Controller
                                  name='acceptTerms'
                                  control={control}
                                  defaultValue={''}
                                  render={({ field, fieldState: { error } }) => (
                                      <FormControl fullWidth error={!!error} onFocus={() => setFocused('agreeTerms')}>
                                          <FormLabel>AGB / Widerruf:</FormLabel>
                                          <FormControlLabel
                                              {...field}
                                              control={<PrimaryCheckBox />}
                                              style={{marginTop: 8}}
                                              label={'Ich stimme den AGB und dem Widerrufsrecht von Nachsenden.info zu.'}
                                          />
                                      </FormControl>
                                  )}
                                rules={{
                                    required: 'Dieses Feld wird benötigt.',
                                }}
                              />
                            </Grid>

                            <Grid item xs={12} sm={12} style={{marginTop: 16, marginLeft: 8, marginRight: 8}}>
                                <Box display='flex' justifyContent={"space-between"} pt={1}>
                                    <Button
                                        variant='contained'
                                        component={RouterLink}
                                        to='/summary'
                                        style={{ marginLeft: 8 }}>
                                        zurück
                                    </Button>
                                    <Button
                                        type='submit'
                                        variant='contained'
                                        style={{
                                            color: COLOR_WHITE,
                                            backgroundColor: COLOR_PRIMARY_DARK
                                        }}>
                                        Bestellung aufgeben
                                    </Button>
                                </Box>
                            </Grid>
                          </Grid>
                        </form>
                        </FormProvider>
                      </Grid>
                      <Grid item sm={12} md={5} style={{borderLeft: '1px solid lightgray', marginTop: 32, marginBottom: 32}}>
                          <Box sx={{
                              display: 'flex',
                              justifyContent: 'center',
                              alignItems: 'center',
                              width: '100%',
                              height: '100%',
                              flexDirection: 'column'
                          }}>
                                  <HelpPanel field={focused} />
                          </Box>
                      </Grid>
                    </Grid>
                </Paper>
            </Box>
        </Container>
    );
};

export default PaymentScreen;