import axios from 'axios';
import {
    CREATE_PAYMENT_FAIL,
    CREATE_PAYMENT_REQUEST,
    CREATE_PAYMENT_SUCCESS, INIT_PAYPAL_REQUEST, INIT_PAYPAL_RESET, INIT_PAYPAL_SUCCESS, INVOICE_FAIL,
    INVOICE_REQUEST,
    INVOICE_SUCCESS,
    ORDER_PLACE_FAIL,
    ORDER_PLACE_REQUEST,
    ORDER_PLACE_SUCCESS,
} from '../constants/order.constant';
// import { getCookie } from '../utils';
import { getStripe } from '../stripe';

// const API_URL = 'http://localhost:8000/';
// const csrftoken = getCookie('csrftoken');
axios.defaults.withCredentials = true;
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN';
axios.defaults.xsrfCookieName = 'csrftoken';

export const createPayment = ( paymentData ) => async (dispatch) => {
    try {
        dispatch({ type: CREATE_PAYMENT_REQUEST });

        const config = {
            headers: {
              'Content-Type': 'application/json',
            //   "X-CSRFToken": csrftoken
            },
        };
        const { data } = await axios.post(
            `/api/create-checkout-session`,
            paymentData,
            config
        );

        // CREATE PAYMENT SESSION SUCCESS
        dispatch({
            type: CREATE_PAYMENT_SUCCESS,
            payload: data
        });
        const stripe = await getStripe();
        stripe.redirectToCheckout({
            sessionId: data.id
        });
    } catch (error) {
        dispatch({
            type: CREATE_PAYMENT_FAIL,
            payload:
                error.response && error.response.data.message
                ? error.response.data.message
                : error.message,
        });
    }
}

export const initPaypal = () => async (dispatch) => {
    try {
        dispatch({ type: INIT_PAYPAL_REQUEST });

        const { data } = await axios.get(
            `/api/paypal`,
        );

        dispatch({
            type: INIT_PAYPAL_SUCCESS,
            payload: data
        });
    } catch (error) {
        dispatch({
            type: INIT_PAYPAL_RESET,
            payload:
                error.response && error.response.data.message
                ? error.response.data.message
                : error.message,
        });
    }
}

