import React from 'react';
import {Box, Container, Grid, makeStyles, Paper, Typography} from '@material-ui/core';

import Meta from '../components/Meta';

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    boxContainer: {
        position: 'relative',
        ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
        padding: '24px 10%',
        height: '100%',
        [theme.breakpoints.down('xs')]: {
          padding: '24px 5%',
        },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
    bold: {
        fontWeight: "bold"
    }
}));

const RecallScreen = ({ history }) => {
    const classes = useStyles();

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}></Grid>
            <Box className={classes.boxContainer}>
                <Paper elevation={0} className={classes.content} square>
                    <Grid container spacing={4}>
                        <Grid item xs={12}>
                            <Typography variant='subtitle1' gutterBottom> Stand: November / 2021 </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> Sie haben das Recht, binnen 14 Tagen ohne Angaben von Gründen den Vertrag zu widerrufen. Die Widerrufsfrist beginnt mit dem Tag des Vertragsschlusses. Das Widerrufsrecht erlischt § 356 Absatz 4 BGB bereits vor Ablauf von 14 Tagen, wenn Sie dem sofortigen Beginn mit der Dienstleistung zugestimmt haben, mit der Ausführung erst nach dieser Zustimmung begonnen wurde und die Dienstleistung vollständig erbracht worden ist. </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> Um Ihr Widerrufsrecht auszuüben, müssen Sie uns (DDV Deutscher Dienstleistungsverlag UG (Haftungsbeschränkt) Fellbacher Str. 23 13469 Berlin , eMail: info@nachsendebeauftragung.de) mittels einer eindeutigen Erklärung (z. B. ein mit der Post versandter Brief, Telefax oder E-Mail) über Ihren Entschluss, diesen Vertrag zu widerrufen, informieren. Sie können dafür das beigefügte Muster-Widerrufsformular verwenden, das jedoch nicht vorgeschrieben ist. </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> Zur Wahrung der Widerrufsfrist reicht es aus, dass Sie die Mitteilung über die Ausübung des Widerrufsrechts vor Ablauf der Widerrufsfrist absenden. </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> Folgen des Widerrufs </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> Wenn Sie diesen Vertrag widerrufen, haben wir Ihnen alle Zahlungen, die wir von Ihnen erhalten haben, einschließlich der Lieferkosten (mit Ausnahme der zusätzlichen Kosten, die sich daraus ergeben, dass Sie eine andere Art der Lieferung als die von uns angebotene, günstigste Standardlieferung gewählt haben), unverzüglich und spätestens binnen vierzehn Tagen ab dem Tag zurückzuzahlen, an dem die Mitteilung über Ihren Widerruf dieses Vertrags bei uns eingegangen ist. Für diese Rückzahlung verwenden wir dasselbe Zahlungsmittel, das Sie bei der ursprünglichen Transaktion eingesetzt haben, es sei denn, mit Ihnen wurde ausdrücklich etwas anderes vereinbart; in keinem Fall werden Ihnen wegen dieser Rückzahlung Entgelte berechnet. </Typography>
                            <Typography variant='body1' gutterBottom> Haben Sie verlangt, dass die Dienstleistungen während der Widerrufsfrist beginnen sollen, so haben Sie uns einen angemessenen Betrag zu zahlen, der dem Anteil der bis zu dem Zeitpunkt, zu dem Sie uns von der Ausübung des Widerrufsrechts hinsichtlich dieses Vertrags unterrichten, bereits erbrachten Dienstleistungen im Vergleich zum Gesamtumfang der im Vertrag vorgesehenen Dienstleistungen entspricht. </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> Muster Widerrufsformular </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> (Wenn Sie den Vertrag widerrufen wollen, dann füllen Sie bitte dieses Formular aus und senden Sie es zurück.) </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> An: </Typography>
                            <Typography variant='body1' gutterBottom> DDV Deutscher Dienstleistungsverlag UG (Haftungsbeschränkt) </Typography>
                            <Typography variant='body1' gutterBottom> Fellbacher Str. 23 </Typography>
                            <Typography variant='body1' gutterBottom> 13469 Berlin </Typography>
                            <Typography variant='body1' gutterBottom> info@nachsendebeauftragung.de </Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography variant='body1' gutterBottom> Hiermit widerrufe(n) ich/wir (*) den von mir/uns (*) abgeschlossenen Vertrag über den Kauf der folgenden Waren (*)/die Erbringung der folgenden Dienstleistung (*) </Typography>
                            <Typography variant='body1' gutterBottom> Bestellt am (*)/erhalten am (*): ___.___._______ </Typography>
                            <Typography variant='body1' gutterBottom> Name des/der Verbraucher(s): ________________ _________________ </Typography>
                            <Typography variant='body1' gutterBottom> Anschrift des/der Verbraucher(s): ______________________________________ </Typography>
                            <Typography variant='body1' gutterBottom> Unterschrift des/der Verbraucher(s) (nur bei Mitteilung auf Papier): _______________________ </Typography>
                            <Typography variant='body1' gutterBottom> Datum: ____.____.________ </Typography>
                            <Typography variant='body1' gutterBottom> (*) Unzutreffendes streichen. </Typography>
                        </Grid>
                    </Grid>
                </Paper>
            </Box>
        </Container>
    );
};

export default RecallScreen;