import React, { useEffect, useState } from 'react';
import { alpha, Box, Breadcrumbs, Button, Container, Divider, FormControl, FormHelperText, Grid, InputBase, InputLabel, makeStyles, Paper, Typography } from '@material-ui/core';
import { Controller, FormProvider, useForm } from 'react-hook-form';
import { Link as RouterLink } from 'react-router-dom';
import { withStyles } from '@material-ui/styles';
import { CountryDropdown } from 'react-country-region-selector';
import AddBoxIcon from '@material-ui/icons/AddBox';
import IndeterminateCheckBoxIcon from '@material-ui/icons/IndeterminateCheckBox';
import validator from 'validator';
import lookup from 'country-code-lookup';

import Meta from '../components/Meta';
import OrderSteps from '../components/OrderSteps';
import HelpPanel from '../components/HelpPanel';
import { ORDER_INFO, ORDER_REASON_ABSENCE_VACATION, ORDER_REASON_MOVE } from '../constants/key.constant';
import { COLOR_PRIMARY_DARK, COLOR_WHITE } from '../constants/color.constant';
import NoParcelOrInfopostForOverseasDialog from '../components/dialog/NoParcelOrInfopostForOverseasDialog';
import ReasonMoveOrAbsenceForOverseasDialog from '../components/dialog/ReasonMoveOrAbsenceForOverseasDialog';

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    form: {
      '& > *': {
        marginBottom: 16,
      },
    },
    boxContainer: {
      position: 'relative',
      ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
      padding: '24px 10%',
      height: '100%',
      [theme.breakpoints.down('xs')]: {
        padding: '24px',
      },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
}));

const Input = withStyles((theme) => ({
    root: {
        'label + &': {
            marginTop: theme.spacing(3),
        },
        '&.Mui-error $input': {
            borderColor: '#f44336',
        },
    },
    input: {
        borderRadius: 4,
        position: 'relative',
        backgroundColor: theme.palette.common.white,
        border: '1px solid #ced4da',
        fontSize: 16,
        padding: '10px 12px',
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        '&:focus': {
            boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
            borderColor: theme.palette.primary.main,
        },
    },
}))(InputBase);

const AddressScreen = ({ history }) => {
    const classes = useStyles();
    const methods = useForm({ shouldUnregister: true });
    const { handleSubmit, control, setValue } = methods;
    const [focused, setFocused] = useState('');
    const hiddenKeys = [
        'addressSupplement',
        'addressAdditionalPoBox',
        'addressAdditionalZipCode',
        'addressAdditionalCity',
        'newAddressSupplement',
        'newAddressAdditionalPoBox',
        'newAddressAdditionalZipCode',
        'newAddressAdditionalCity',
    ];

    // Local Storage Data
    let localData = localStorage.getItem(ORDER_INFO);
    if (localData) {
        localData = JSON.parse(localData);
        if (!localData.customer_type) {
            history.push('/');
        }
        if (!localData.salutation) {
            history.push('/receiver');
        }
    } else {
        history.push('/');
    }
    
    const [newCountry, setNewCountry] = useState(localData && localData.new_country ? localData.new_country : 'Germany');
    const [oldSupplement, setOldSupplement] = useState(localData && localData.address_supplement ? true : false);
    const [oldAdditionalMailBox, setOldAdditionalMailBox] = useState(localData && localData.address_additional_pobox ? true : false);
    const [newSupplement, setNewSupplement] = useState(localData && localData.new_address_supplement ? true : false);
    const [newAdditionalMailBox, setNewAdditionalMailBox] = useState(localData && localData.new_address_additional_pobox ? true : false);
    const [openNoParcelModal, setNoParcelModal] = useState(false);
    const [openWrongReasonModal, setWrongReasonModal] = useState(false);
    const handleOpenWrongReasonModal = () => setWrongReasonModal(true);
    const handleCloseWrongReasonModal = () => setWrongReasonModal(false);
    const handleOpenNoParcelModal = () => setNoParcelModal(true);
    const handleCloseNoParcelModal = () => {
        setNoParcelModal(false);
        history.push('/summary');
    };
    const [countryForModal, setCountryForModal] = useState('');

    useEffect(() => {
        setValue('new_country', newCountry);
    }, [ newCountry, setValue ]);

    const checkPosalCode = (v) => {
        return validator.isPostalCode(v, 'DE') ? true : 'Adresse muss 5 Stellen enthalten.';
    };

    const checkPosalCodeWithNewCountry = (v) => {
        let countryCode = lookup.byCountry(newCountry).internet;
        countryCode = validator.isFloatLocales.indexOf(countryCode) > -1 ? countryCode : 'DE';
        return validator.isPostalCode(v, countryCode) ? true : 'Adresse muss 5 Stellen enthalten.';
    };

    const checkPoBox = (v) => {
        return v.length >= 2 && v.length <= 6 ? true : 'Die Nummer des neuen Postfach muss 2-6 Stellen haben.';
    };

    const onNext = (data) => {
        let isModal = false;
        Object.keys(data).map(key => delete localData[key]);
        hiddenKeys.map(key => delete localData[key]);
        localData = { ...localData, ...data};

        if (localData.new_country !== 'Germany') {   //International
            if (localData.order_reason !== ORDER_REASON_MOVE && localData.order_reason !== ORDER_REASON_ABSENCE_VACATION) {
                setCountryForModal(localData.new_country);
                handleOpenWrongReasonModal();
                return;
            }
            if (localData.parcel_shipments || localData.dhl_infopost) {
                setCountryForModal(localData.new_country);
                localData.parcel_shipments = false;
                localData.dhl_infopost = false;
                handleOpenNoParcelModal();
                isModal = true;
            }
        }
        localData.new_countryCode = lookup.byCountry(localData.new_country).internet;
        localData.address = `${data.addressStreet} ${data.addressHouse}, ${data.addressZipCode} ${data.addressCity}`;
        localData.new_address = `${data.newAddressStreet} ${data.newAddressHouse}, ${data.newAddressZipCode} ${data.newAddressCity}, ${newCountry}`;
        console.log(localData);

        localStorage.setItem(ORDER_INFO, JSON.stringify(localData));
        if (!isModal) {
            history.push('/summary');
        }
    };

    useEffect(() => {
        console.log(openNoParcelModal)
    }, [openNoParcelModal]);

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}>
                <Grid item xs={12}>
                    <Breadcrumbs style={{ marginBottom: 24 }}/>
                    <OrderSteps step={2} />
                </Grid>
            </Grid>
            <Box className={classes.boxContainer}>
                <Paper elevation={0} className={classes.content} square>
                    <Grid container spacing={8}>
                        <Grid item sm={12} md={7}>
                            <FormProvider {...methods}>
                                <form className={classes.form} onSubmit={handleSubmit(onNext)}>
                                    <Grid container spacing={2}>
                                        <Typography variant='h5' gutterBottom>
                                            {'Ihre alte Anschrift'}
                                        </Typography>
                                        <Grid item xs={12} sm={9}>
                                            <Controller
                                                name='addressStreet'
                                                control={control}
                                                defaultValue={localData && localData.addressStreet ? localData.addressStreet : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-addressstreet'>
                                                            {'Straße  *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-addressstreet' placeholder='Straße' onFocus={() => setFocused('addressStreetHouse')}/>{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid>
                                        <Grid item xs={12} sm={3}>
                                            <Controller
                                                name='addressHouse'
                                                control={control}
                                                defaultValue={localData && localData.addressHouse ? localData.addressHouse : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-addresshouse'>
                                                            {'Haus-Nr. *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-addresshouse' placeholder='Nr.' onFocus={() => setFocused('addressStreetHouse')} />{' '}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid>

                                        {localData && (localData.order_reason === ORDER_REASON_MOVE || localData.order_reason === ORDER_REASON_ABSENCE_VACATION) && 
                                            <Grid item xs={12} style={{ marginTop: -8, marginBottom: -8, paddingBottom: 0 }}>
                                                <Box display='flex' justifyContent={"flex-end"}>
                                                    <div style={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                        flexWrap: 'wrap',
                                                        cursor: 'pointer',
                                                        color: COLOR_PRIMARY_DARK
                                                    }} onClick={() => {
                                                        setOldSupplement(!oldSupplement);
                                                    }}>
                                                        {oldSupplement ? <IndeterminateCheckBoxIcon /> : <AddBoxIcon />}<span>{' Adresszusatz hinzufügen'}</span>
                                                    </div>  
                                                </Box>
                                            </Grid>
                                         }

                                        { oldSupplement && <Grid item xs={12}>
                                            <Controller
                                                name='address_supplement'
                                                control={control}
                                                defaultValue={localData && localData.address_supplement ? localData.address_supplement : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-addresssupplement'>
                                                            {'Adresszusatz *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-addresssupplement' placeholder='Adresszusatz (z.B. c/o ...)' onFocus={() => setFocused('addressSupplement')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid> }

                                        <Grid item xs={12} sm={3}>
                                            <Controller
                                                name='addressZipCode'
                                                control={control}
                                                defaultValue={localData && localData.addressZipCode ? localData.addressZipCode : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-addresszipcode'>
                                                            {'PLZ *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-addresszipcode' placeholder='PLZ' onFocus={() => setFocused('addressZipCodeCity')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                    validate: checkPosalCode
                                                }} 
                                                />
                                        </Grid>
                                        <Grid item xs={12} sm={9}>
                                            <Controller
                                                name='addressCity'
                                                control={control}
                                                defaultValue={localData && localData.addressCity ? localData.addressCity : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-addresscity'>
                                                            {'Ort *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-addresscity' placeholder='Ort' onFocus={() => setFocused('addressZipCodeCity')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid>

                                        <Grid item xs={12} style={{ marginTop: -8, marginBottom: -8 }}>
                                            <Box display='flex' justifyContent={"flex-end"}>
                                                    <div style={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                        flexWrap: 'wrap',
                                                        cursor: 'pointer',
                                                        color: COLOR_PRIMARY_DARK
                                                    }} onClick={() => {
                                                        setOldAdditionalMailBox(!oldAdditionalMailBox);
                                                    }}>
                                                        {oldAdditionalMailBox ? <IndeterminateCheckBoxIcon /> : <AddBoxIcon />}<span>{' Zusätzlich Postfach'}</span>
                                                    </div>  
                                            </Box>
                                        </Grid>
                                        {oldAdditionalMailBox && <>
                                            <Grid item xs={12}>
                                                <Controller
                                                    name='address_additional_pobox'
                                                    control={control}
                                                    defaultValue={localData && localData.address_additional_pobox ? localData.address_additional_pobox : ''}
                                                    render={({ field, fieldState: { error } }) => (
                                                        <FormControl fullWidth error={!!error}>
                                                            <InputLabel shrink htmlFor='form-addressadditionalpobox'>
                                                                {'Postfach-Nr. *'}
                                                            </InputLabel>
                                                            <Input {...field} id='form-addressadditionalpobox' placeholder='Postfach-Nummer' 
                                                                type='tel' onInput = {(e) =>{
                                                                    e.target.value = e.target.value.length > 0 ? Math.max(0, parseInt(e.target.value.replace(/[^0-9]/g, '')) ).toString() : ''
                                                                    if (e.target.value === 'NaN') {
                                                                        e.target.value = ''
                                                                }}}
                                                                onFocus={() => setFocused('addressPoBox')} />{' '}
                                                            {error && (
                                                                <FormHelperText error>{error.message}</FormHelperText>
                                                            )}
                                                        </FormControl>
                                                    )}
                                                    rules={{ 
                                                        required: 'Dieses Feld wird benötigt.',
                                                        validate: checkPoBox
                                                    }} 
                                                    />
                                            </Grid>
                                            <Grid item xs={12} sm={3}>
                                                <Controller
                                                    name='address_additional_zipcode'
                                                    control={control}
                                                    defaultValue={localData && localData.address_additional_zipcode ? localData.address_additional_zipcode : ''}
                                                    render={({ field, fieldState: { error } }) => (
                                                        <FormControl fullWidth error={!!error}>
                                                            <InputLabel shrink htmlFor='form-addressadditionalzipcode'>
                                                                {'PLZ *'}
                                                            </InputLabel>
                                                            <Input {...field} id='form-addressadditionalzipcode' placeholder='PLZ' onFocus={() => setFocused('addressZipCodeCity')} />{' '}
                                                            {error && (
                                                                <FormHelperText error>{error.message}</FormHelperText>
                                                            )}
                                                        </FormControl>
                                                    )}
                                                    rules={{ 
                                                        required: 'Dieses Feld wird benötigt.',
                                                        validate: checkPosalCode
                                                    }} 
                                                    />
                                            </Grid>
                                            <Grid item xs={12} sm={9}>
                                                <Controller
                                                    name='address_additional_city'
                                                    control={control}
                                                    defaultValue={localData && localData.address_additional_city ? localData.address_additional_city : ''}
                                                    render={({ field, fieldState: { error } }) => (
                                                        <FormControl fullWidth error={!!error}>
                                                            <InputLabel shrink htmlFor='form-addressadditionalcity'>
                                                                {'Ort *'}
                                                            </InputLabel>
                                                            <Input {...field} id='form-addressadditionalcity' placeholder='Ort' onFocus={() => setFocused('addressZipCodeCity')} />{' '}
                                                            {error && (
                                                                <FormHelperText error>{error.message}</FormHelperText>
                                                            )}
                                                        </FormControl>
                                                    )}
                                                    rules={{ 
                                                        required: 'Dieses Feld wird benötigt.',
                                                    }} 
                                                    />
                                            </Grid>
                                        </>}
                                        

                                        <Grid item xs={12}>
                                            <Divider style={{marginTop: 2, marginBottom: 2}} />
                                        </Grid>

                                        <Typography variant='h5' gutterBottom>
                                            {'Ihre neue Anschrift'}
                                        </Typography>
                                        <Grid item xs={12} sm={9}>
                                            <Controller
                                                name='new_country'
                                                control={control}
                                                defaultValue={localData && localData.new_country ? localData.new_country : ''}
                                                render={({ field: {value}, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-newcountry'>
                                                            {'Land'}
                                                        </InputLabel>
                                                        <CountryDropdown
                                                            value={value}
                                                            disabled={localData && localData.order_reason && localData.order_reason !== ORDER_REASON_MOVE && localData.order_reason !== ORDER_REASON_ABSENCE_VACATION}
                                                            onChange={setNewCountry}
                                                            onFocus={() => setFocused('Land')}
                                                            whitelist={['DE']}
                                                            style={{
                                                                marginTop: "24px",
                                                                width: "100%",
                                                                height: "42px"
                                                            }}
                                                        />
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid><Grid item xs={12} sm={3}>
                                        </Grid>
                                        <Grid item xs={12} sm={9}>
                                            <Controller
                                                name='newAddressStreet'
                                                control={control}
                                                defaultValue={localData && localData.newAddressStreet ? localData.newAddressStreet : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-newaddressstreet'>
                                                            {'Straße  *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-newaddressstreet' placeholder='Straße' onFocus={() => setFocused('newAddressStreetHouse')}/>{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid>
                                        <Grid item xs={12} sm={3}>
                                            <Controller
                                                name='newAddressHouse'
                                                control={control}
                                                defaultValue={localData && localData.newAddressHouse ? localData.newAddressHouse : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-newaddresshouse'>
                                                            {'Haus-Nr. *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-newaddresshouse' placeholder='Nr.' onFocus={() => setFocused('newAddressStreetHouse')} />{' '}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid>
                                        {localData && (localData.order_reason === ORDER_REASON_MOVE || localData.order_reason === ORDER_REASON_ABSENCE_VACATION) && 
                                            <Grid item xs={12} style={{ marginTop: -8, marginBottom: -8, paddingBottom: 0 }}>
                                                <Box display='flex' justifyContent={"flex-end"}>
                                                        <div style={{
                                                            display: 'flex',
                                                            alignItems: 'center',
                                                            flexWrap: 'wrap',
                                                            cursor: 'pointer',
                                                            color: COLOR_PRIMARY_DARK
                                                        }} onClick={() => {
                                                            setNewSupplement(!newSupplement);
                                                        }}>
                                                            {newSupplement ? <IndeterminateCheckBoxIcon /> : <AddBoxIcon />}<span>{' Adresszusatz hinzufügen'}</span>
                                                        </div>  
                                                </Box>
                                            </Grid>
                                        }
                                        { newSupplement && <Grid item xs={12}>
                                            <Controller
                                                name='new_address_supplement'
                                                control={control}
                                                defaultValue={localData && localData.new_address_supplement ? localData.new_address_supplement : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-newaddresssupplement'>
                                                            {'Adresszusatz *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-newaddresssupplement' placeholder='Adresszusatz (z.B. c/o ...)' onFocus={() => setFocused('addressSupplement')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid> }

                                        <Grid item xs={12} sm={3}>
                                            <Controller
                                                name='newAddressZipCode'
                                                control={control}
                                                defaultValue={localData && localData.newAddressZipCode ? localData.newAddressZipCode : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-newaddresszipcode'>
                                                            {'PLZ *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-newaddresszipcode' placeholder='PLZ' onFocus={() => setFocused('newAddressZipCodeCity')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                    validate: checkPosalCodeWithNewCountry
                                                }} 
                                                />
                                        </Grid>
                                        <Grid item xs={12} sm={9}>
                                            <Controller
                                                name='newAddressCity'
                                                control={control}
                                                defaultValue={localData && localData.newAddressCity ? localData.newAddressCity : ''}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='form-newaddresscity'>
                                                            {'Ort *'}
                                                        </InputLabel>
                                                        <Input {...field} id='form-newaddresscity' placeholder='Ort' onFocus={() => setFocused('newAddressZipCodeCity')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{ 
                                                    required: 'Dieses Feld wird benötigt.',
                                                }} 
                                                />
                                        </Grid>
                                        <Grid item xs={12} style={{ marginTop: -8, marginBottom: -8 }}>
                                            <Box display='flex' justifyContent={"flex-end"}>
                                                    <div style={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                        flexWrap: 'wrap',
                                                        cursor: 'pointer',
                                                        color: COLOR_PRIMARY_DARK
                                                    }} onClick={() => {
                                                        setNewAdditionalMailBox(!newAdditionalMailBox);
                                                    }}>
                                                        {newAdditionalMailBox ? <IndeterminateCheckBoxIcon /> : <AddBoxIcon />}<span>{' Zusätzlich Postfach'}</span>
                                                    </div>  
                                            </Box>
                                        </Grid>

                                        {newAdditionalMailBox && <>
                                            <Grid item xs={12}>
                                                <Controller
                                                    name='new_address_additional_pobox'
                                                    control={control}
                                                    defaultValue={localData && localData.new_address_additional_pobox ? localData.new_address_additional_pobox : ''}
                                                    render={({ field, fieldState: { error } }) => (
                                                        <FormControl fullWidth error={!!error}>
                                                            <InputLabel shrink htmlFor='form-newaddressadditionalpobox'>
                                                                {'Postfach-Nr. *'}
                                                            </InputLabel>
                                                            <Input {...field} id='form-newaddressadditionalpobox' placeholder='Postfach-Nummer' 
                                                                type='tel' onInput = {(e) =>{
                                                                    e.target.value = e.target.value.length > 0 ? Math.max(0, parseInt(e.target.value.replace(/[^0-9]/g, '')) ).toString() : ''
                                                                    if (e.target.value === 'NaN') {
                                                                        e.target.value = ''
                                                                }}}
                                                                onFocus={() => setFocused('newAddressPoBox')} />{' '}
                                                            {error && (
                                                                <FormHelperText error>{error.message}</FormHelperText>
                                                            )}
                                                        </FormControl>
                                                    )}
                                                    rules={{ 
                                                        required: 'Dieses Feld wird benötigt.',
                                                        validate: checkPoBox
                                                    }} 
                                                    />
                                            </Grid>
                                            <Grid item xs={12} sm={3}>
                                                <Controller
                                                    name='new_address_additional_zipcode'
                                                    control={control}
                                                    defaultValue={localData && localData.new_address_additional_zipcode ? localData.new_address_additional_zipcode : ''}
                                                    render={({ field, fieldState: { error } }) => (
                                                        <FormControl fullWidth error={!!error}>
                                                            <InputLabel shrink htmlFor='form-newaddressadditionalzipcode'>
                                                                {'PLZ *'}
                                                            </InputLabel>
                                                            <Input {...field} id='form-newaddressadditionalzipcode' placeholder='PLZ' onFocus={() => setFocused('newAddressZipCodeCity')} />{' '}
                                                            {error && (
                                                                <FormHelperText error>{error.message}</FormHelperText>
                                                            )}
                                                        </FormControl>
                                                    )}
                                                    rules={{ 
                                                        required: 'Dieses Feld wird benötigt.',
                                                        validate: checkPosalCodeWithNewCountry
                                                    }} 
                                                    />
                                            </Grid>
                                            <Grid item xs={12} sm={9}>
                                                <Controller
                                                    name='new_address_additional_city'
                                                    control={control}
                                                    defaultValue={localData && localData.new_address_additional_city ? localData.new_address_additional_city : ''}
                                                    render={({ field, fieldState: { error } }) => (
                                                        <FormControl fullWidth error={!!error}>
                                                            <InputLabel shrink htmlFor='form-newaddressadditionalcity'>
                                                                {'Ort *'}
                                                            </InputLabel>
                                                            <Input {...field} id='form-newaddressadditionalcity' placeholder='Ort' onFocus={() => setFocused('newAddressZipCodeCity')} />{' '}
                                                            {error && (
                                                                <FormHelperText error>{error.message}</FormHelperText>
                                                            )}
                                                        </FormControl>
                                                    )}
                                                    rules={{ 
                                                        required: 'Dieses Feld wird benötigt.',
                                                    }} 
                                                    />
                                            </Grid>
                                        </>}

                                        <Grid item xs={12} sm={12} style={{marginTop: 8}}>
                                            <Box display='flex' justifyContent={"space-between"} pt={1}>
                                                <Button 
                                                    variant='contained'
                                                    component={RouterLink}
                                                    to='/receiver'
                                                    style={{ marginLeft: 8 }}>
                                                    zurück
                                                </Button>
                                                <Button 
                                                    type='submit'
                                                    variant='contained'
                                                    style={{
                                                        color: COLOR_WHITE,
                                                        backgroundColor: COLOR_PRIMARY_DARK
                                                    }}>
                                                    weiter
                                                </Button>
                                            </Box>
                                        </Grid>
                                    </Grid>
                                </form>
                            </FormProvider>
                        </Grid>
                        <Grid item sm={12} md={5} style={{borderLeft: '1px solid lightgray', marginTop: 32, marginBottom: 32}}>
                            <Box sx={{
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                width: '100%',
                                height: '100%',
                                flexDirection: 'column'
                            }}>
                                <HelpPanel field={focused} />
                            </Box>
                        </Grid>
                    </Grid>
                </Paper>
            </Box>
            <NoParcelOrInfopostForOverseasDialog 
                openModal={openNoParcelModal}
                handleCloseModal={handleCloseNoParcelModal}
                country={countryForModal}
            />
            <ReasonMoveOrAbsenceForOverseasDialog 
                openModal={openWrongReasonModal}
                handleCloseModal={handleCloseWrongReasonModal}
                country={countryForModal}
            />
        </Container>
    );
};

export default AddressScreen;