import React from 'react';
import { Box, Breadcrumbs, Button, Container, Divider, FormControl, Grid, InputLabel, makeStyles, Paper, Typography } from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';

import Meta from '../components/Meta';
import OrderSteps from '../components/OrderSteps';
import { COLOR_PRIMARY_DARK, COLOR_WHITE } from '../constants/color.constant';
import { Controller, FormProvider, useForm } from 'react-hook-form';
import { CUSTOMER_TYPE_BUSINESS, CUSTOMER_TYPE_PRIVATE, NOT_SELECTED, ORDER_INFO, SELECTED } from '../constants/key.constant';
import { getCustomerType, getOrderReason } from '../utils';

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    form: {
      '& > *': {
        marginBottom: 16,
      },
    },
    boxContainer: {
      position: 'relative',
      ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
      padding: '24px 10%',
      height: '100%',
      [theme.breakpoints.down('xs')]: {
        padding: '24px',
      },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
}));

const wrapWithBold = (props) => {
    return <span style={{fontWeight: "bold"}}>
        {props}
    </span>
}

const SummaryScreen = ({ history }) => {
    const classes = useStyles();
    const methods = useForm();
    const { handleSubmit, setValue, control } = methods;

    let oldTwoShowMode = false;
    let newTwoShowMode = false;

    // Local Storage Data
    let localData = localStorage.getItem(ORDER_INFO);
    if (localData) {
        localData = JSON.parse(localData);
        if (!localData.customer_type) {
            history.push('/');
        }
        if (!localData.salutation) {
            history.push('/receiver');
        }
        if (!localData.addressStreet) {
            history.push('/address');
        }

        Object.keys(localData).map(key => setValue(key, localData[key]));
        setValue('customer_type', getCustomerType(localData.customer_type));
        setValue('order_reason', getOrderReason(localData.order_reason));
        setValue('parcel_shipments', localData.parcel_shipments ? SELECTED : NOT_SELECTED);
        setValue('dhl_infopost', localData.dhl_infopost ? SELECTED : NOT_SELECTED);

        if (localData.customer_type === CUSTOMER_TYPE_BUSINESS) {
          setValue('businessName', `${localData.salutation} ${localData.nameFirst} ${localData.nameLast}`);
        }

        if (localData.address_additional_pobox) {
          oldTwoShowMode = true;
        }
        if (localData.new_address_additional_pobox) {
          newTwoShowMode = true;
        }
        // if (localData.additionalRecipients) {
        //   localData.additionalRecipients.map((name, key) => {
        //     setValue(`Weitere ${parseInt(key) + 1}. Person`, `${name.first} ${name.last}`);
        //   });
        // }
    } else {
      history.push('/');
  }


    const onNext = (data) => {
      console.log(data);
      localData.additional_recipients.forEach(name => delete data[`additionalRecipients_${name.idx}`])
      localData.additional_recipients = localData.additional_recipients.map(name => `${name.first} ${name.last}`);
      localStorage.setItem(ORDER_INFO, JSON.stringify(localData));
      history.push('/payment');
    };

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}>
                <Grid item xs={12}>
                    <Breadcrumbs style={{ marginBottom: 24 }}/>
                    <OrderSteps step={3} />
                </Grid>
            </Grid>
            <Box className={classes.boxContainer}>
                <Paper elevation={0} className={classes.content} square>
                  <FormProvider {...methods}>
                  <form className={classes.form} onSubmit={handleSubmit(onNext)}>
                    <Grid container spacing={1}>
                      <Grid item xs={12}>
                        <Typography variant='h5' gutterBottom>
                            {'Zusammenfassung'}
                        </Typography> <br />
                        <Typography variant='h6' gutterBottom>
                            {'Bitte kontrollieren Sie nochmal Ihre Eingaben'}
                        </Typography>
                        <Typography variant='subtitle1' gutterBottom>
                            Sind Ihre {wrapWithBold("Daten korrekt")}? Klicken Sie gerne auf {wrapWithBold("Zusammenfassung und Abschluss")} und fahren Sie gerne fort.
                        </Typography>
                        <Typography variant='subtitle1' gutterBottom>
                            Sind Ihre {wrapWithBold("Daten nicht korrekt")}? Klicken Sie bitte auf {wrapWithBold("zurück")} und ändern {wrapWithBold("korrigieren")} diese.
                        </Typography>
                      </Grid>

                      <Grid item xs={12} sm={12} style={{marginTop: 8, marginLeft: 8, marginRight: 8}}>
                          <Box display='flex' justifyContent={"space-between"} pt={1}>
                              <Button 
                                  variant='contained'
                                  component={RouterLink}
                                  to='/address'
                                  style={{ marginLeft: 8 }}>
                                  zurück
                              </Button>
                              <Button 
                                  type='submit'
                                  variant='contained'
                                  style={{
                                      color: COLOR_WHITE,
                                      backgroundColor: COLOR_PRIMARY_DARK
                                  }}>
                                  Zusammenfassung und Abschluss
                              </Button>
                          </Box>
                      </Grid>
                      <Grid item xs={12}>
                          <Divider style={{marginTop: 8, marginBottom: 8}} />
                      </Grid>
                      <Grid item xs={12}>
                        <Grid container spacing={2}>
                          <Grid item xs={12} sm={6}>
                            <Typography variant='h5' gutterBottom>
                                {'Ihre alte Anschrift:'}
                            </Typography> <br />
                            <Divider style={{ marginTop:-8, marginBottom: 16 }} />
                            { oldTwoShowMode ? (
                              <Grid container spacing={1}>
                                <Grid item xs={12}>
                                  <Controller
                                    name='address_additional_pobox'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Altes Postfach (Nr.)'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='address_additional_zipcode'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Alte PLZ'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='address_additional_city'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Alte Stadt'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                {localData && localData.address_supplement && <Grid item xs={12}>
                                  <Controller
                                    name='address_supplement'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Alter Adresszusatz'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid> }
                                
                              </Grid>
                            ) : (
                              <Grid container spacing={1}>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressStreet'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Straßenname'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressHouse'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Hausnummer'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressZipCode'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'PLZ'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressCity'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Stadt'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                {localData && localData.address_supplement && <Grid item xs={12}>
                                  <Controller
                                    name='address_supplement'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Alter Adresszusatz'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid> }
                                
                              </Grid>
                            ) }
                          </Grid>
                          <Grid item xs={12} sm={6}>
                            <Typography variant='h5' gutterBottom>
                                {'Ihre neue Anschrift:'}
                            </Typography> <br />
                            <Divider style={{ marginTop:-8, marginBottom: 16 }} />
                            { newTwoShowMode ? (
                              <Grid container spacing={1}>
                                <Grid item xs={12}>
                                  <Controller
                                    name='new_address_additional_pobox'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neues Postfach (Nr.)'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressZipCode'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neue PLZ'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressCity'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neue Stadt'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                {localData && localData.new_address_supplement && <Grid item xs={12}>
                                  <Controller
                                    name='new_address_supplement'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neuer Adresszusatz'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid> }
                                
                                <Grid item xs={12}>
                                  <Controller
                                    name='new_country'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neues Land'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                              </Grid>
                            ) : (
                              <Grid container spacing={1}>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressStreet'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Straßenname'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressHouse'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Hausnummer'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressZipCode'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neue PLZ'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressCity'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neue Stadt'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                {localData && localData.new_address_supplement && <Grid item xs={12}>
                                  <Controller
                                    name='new_address_supplement'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neuer Adresszusatz'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                 }
                                <Grid item xs={12}>
                                  <Controller
                                    name='new_country'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Neues Land'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                              </Grid>
                            ) }
                            
                          </Grid>
                        </Grid>

                        <Grid container spacing={2}>
                          {/** OLD ADDR PLUS */}
                          <Grid item xs={12} sm={6}>
                            { oldTwoShowMode && <>
                              <Typography variant='h5' gutterBottom>
                                {'Alternative Adresse'}
                              </Typography> <br />
                              <Divider style={{ marginTop:-8, marginBottom: 16 }} />
                              <Grid container spacing={1}>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressStreet'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Straßenname'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressHouse'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Hausnummer'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressZipCode'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'PLZ'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='addressCity'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Stadt'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                              </Grid>
                            </> }
                          </Grid>
                          {/** NEW ADDR PLUS */}
                          <Grid item xs={12} sm={6}>
                            { newTwoShowMode && <>
                              <Typography variant='h5' gutterBottom>
                                {'Alternative Adresse'}
                              </Typography> <br />
                              <Divider style={{ marginTop:-8, marginBottom: 16 }} />
                              <Grid container spacing={1}>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressStreet'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Straßenname'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='newAddressHouse'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Hausnummer'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='new_address_additional_zipcode'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'PLZ'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='new_address_additional_city'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Stadt'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                              </Grid>
                            </> }
                            </Grid>
                        </Grid>

                        <Grid container spacing={2}>
                          <Grid item xs={12} sm={6}>
                            <Typography variant='h5' gutterBottom>
                                {'Weitere Daten:'}
                            </Typography> <br />
                            <Divider style={{ marginTop:-8, marginBottom: 16 }} />
                            <Grid container spacing={1}>
                              <Grid item xs={12}>
                                <Controller
                                  name='customer_type'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'Nachsende-Typ'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                                {localData && localData.other_service && localData.other_service.length > 0 &&
                                    <Grid item xs={12}>
                                        <Controller
                                          name='other_service'
                                          control={control}
                                          render={({ field: { value }, fieldState: { error } }) => (
                                          <FormControl fullWidth error={!!error}>
                                              <InputLabel shrink>
                                                  {'Weitere Versanddienstleister'}
                                              </InputLabel>
                                              <div style={{marginTop: "16px"}}></div>
                                              {value.map((name, idx) => (
                                                  <Typography variant='h6' key={idx}>{name}</Typography>
                                              ))}
                                          </FormControl>
                                          )}
                                          />
                                      </Grid>
                                }

                              <Grid item xs={12}>
                                <Controller
                                  name='later_start_time'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'Nachsende-Start'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                              {localData && localData.deliver_again_date && <Grid item xs={12}>
                                <Controller
                                  name='deliver_again_date'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'Wieder zustellen ab'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid> }
                              <Grid item xs={12}>
                                <Controller
                                  name='order_reason'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'Nachsendegrund'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                              <Grid item xs={12}>
                                <Controller
                                  name='email'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'E-Mail'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                              <Grid item xs={12}>
                                <Controller
                                  name='phone_number'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'Telefon'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                              <Grid item xs={12}>
                                <Controller
                                  name='parcel_shipments'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'Päckchen und Pakete'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                              <Grid item xs={12}>
                                <Controller
                                  name='dhl_infopost'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'DHL INFOPOST'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                            </Grid>
                          </Grid>
                            {/** PROFILE AREA **/}
                          {localData && localData.customer_type === CUSTOMER_TYPE_PRIVATE ? (
                            <Grid item xs={12} sm={6}>
                              <Typography variant='h5' gutterBottom>
                                  {'Personen:'}
                              </Typography> <br />
                              <Divider style={{ marginTop:-8, marginBottom: 16 }} />
                              <Grid container spacing={1}>
                                <Grid item xs={12}>
                                  <Controller
                                    name='nameFirst'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Vorname'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                  <Controller
                                    name='nameLast'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Nachname'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                                {localData && localData.representative_full_name && 
                                  <Grid item xs={12}>
                                    <Controller
                                      name='representative_full_name'
                                      control={control}
                                      render={({ field: { value }, fieldState: { error } }) => (
                                      <FormControl fullWidth error={!!error}>
                                          <InputLabel shrink>
                                              {'Bevollmächtigter'}
                                          </InputLabel>
                                          <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                      </FormControl>
                                      )}
                                      />
                                  </Grid>
                                }
                                {localData && localData.additional_recipients && 
                                  localData.additional_recipients.map((name, key) => (
                                    <Grid item xs={12} key={name.idx}>
                                      <Controller
                                        name={`additionalRecipients_${name.idx}`}
                                        control={control}
                                        render={({ field: { value }, fieldState: { error } }) => (
                                        <FormControl fullWidth error={!!error}>
                                            <InputLabel shrink>
                                                {`Weitere ${parseInt(key) + 1}. Person`}
                                            </InputLabel>
                                            <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{`${name.first} ${name.last}`}</Typography>
                                        </FormControl>
                                        )}
                                        />
                                    </Grid>
                                  ))
                                }
                              </Grid>
                            </Grid>
                          ) : (
                            <Grid item xs={12} sm={6}>
                            <Typography variant='h5' gutterBottom>
                                {'Firma / Betrieb:'}
                            </Typography> <br />
                            <Divider style={{ marginTop:-8, marginBottom: 16 }} />
                            <Grid container spacing={1}>
                              <Grid item xs={12}>
                                <Controller
                                  name='company_name'
                                  control={control}
                                  render={({ field: { value }, fieldState: { error } }) => (
                                  <FormControl fullWidth error={!!error}>
                                      <InputLabel shrink>
                                          {'Firma'}
                                      </InputLabel>
                                      <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                  </FormControl>
                                  )}
                                  />
                              </Grid>
                              {localData && localData.representative_full_name && 
                                <Grid item xs={12}>
                                  <Controller
                                    name='representative_full_name'
                                    control={control}
                                    render={({ field: { value }, fieldState: { error } }) => (
                                    <FormControl fullWidth error={!!error}>
                                        <InputLabel shrink>
                                            {'Bevollmächtigter'}
                                        </InputLabel>
                                        <Typography variant='h6' gutterBottom  style={{marginTop: '16px'}}>{value}</Typography>
                                    </FormControl>
                                    )}
                                    />
                                </Grid>
                              }
                            </Grid>
                          </Grid>
                          )}
                          
                        </Grid>
                      </Grid>
                      <Grid item xs={12}>
                          <Divider style={{marginTop: 8, marginBottom: 8}} />
                      </Grid>
                      <Grid item xs={12} sm={12} style={{marginTop: 8, marginLeft: 8, marginRight: 8}}>
                          <Box display='flex' justifyContent={"space-between"} pt={1}>
                              <Button 
                                  variant='contained'
                                  component={RouterLink}
                                  to='/address'
                                  style={{ marginLeft: 8 }}>
                                  zurück
                              </Button>
                              <Button 
                                  type='submit'
                                  variant='contained'
                                  style={{
                                      color: COLOR_WHITE,
                                      backgroundColor: COLOR_PRIMARY_DARK
                                  }}>
                                  Zusammenfassung und Abschluss
                              </Button>
                          </Box>
                      </Grid>
                    </Grid>
                  </form>
                  </FormProvider>
                </Paper>
            </Box>
        </Container>
    );
};

export default SummaryScreen;