import React, {useEffect, useState} from "react";
import {
  PaymentElement,
  useStripe,
  useElements
} from "@stripe/react-stripe-js";
import {useSelector} from "react-redux";
import logo from '../../assets/images/stripe.png';
import {Box} from "@material-ui/core";

export default function CheckoutForm() {
  const stripe = useStripe();
  const elements = useElements();

  const [message, setMessage] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [myInvoideId, setMyInvoiceId] = useState('');
  const { loading, error, isSuccess, orderData } = useSelector((state) => state.order);

  useEffect(() => {
    if (isSuccess && orderData) {
      setMyInvoiceId(orderData.invoiceId);
    }
  },[
      orderData, isSuccess
  ]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!stripe || !elements) {
      // Stripe.js has not yet loaded.
      // Make sure to disable form submission until Stripe.js has loaded.
      return;
    }

    setIsLoading(true);

    const { error } = await stripe.processOrder({
      elements,
      confirmParams: {
        // Make sure to change this to your payment completion page
        return_url: "http://sending-service.herokuapp.com/payment/stripe/"+myInvoideId,
        // return_url: "http://localhost:8000/payment/stripe/"+myInvoideId,
      },
    });

    // This point will only be reached if there is an immediate error when
    // confirming the payment. Otherwise, your customer will be redirected to
    // your `return_url`. For some payment methods like iDEAL, your customer will
    // be redirected to an intermediate site first to authorize the payment, then
    // redirected to the `return_url`.
    if (error.type === "card_error" || error.type === "validation_error") {
      setMessage(error.message);
    } else {
      setMessage("An unexpected error occurred.");
    }

    setIsLoading(false);
  };

  return (
    <form id="payment-form" onSubmit={handleSubmit}>
      <Box width={'100%'} display='flex' justifyContent='center' pb={2}>
        <img src={logo} style={{ width: '50%' }} />
      </Box>
      <PaymentElement id="payment-element" />
      <button disabled={isLoading || !stripe || !elements} id="submit" className='stripe-pay-button'>
        <span id="button-text">
          {isLoading ? <div className="spinner" id="spinner"></div> : "Jetzt bezahlen"}
        </span>
      </button>
      {/* Show any error or success messages */}
      {message && <div id="payment-message">{message}</div>}
    </form>
  );
}