import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Typography } from '@material-ui/core';
import React from 'react';

const NoticeOfRelocateDialog = ({ openModal, handleCloseModal }) => {
    return (
        <Dialog
            open={openModal}
            onClose={handleCloseModal}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"Umzugsmitteilung:"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description" component={'div'}>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        {`Ihre neue Anschrift wird an Ihre geschäftlichen Kontakte (z.B. Bank, Versicherung o.Ä. ) mitgeteilt. Diese kennen Ihre alte Anschrift bereits.`}
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        {`Sie erhalten Ihre Post somit an Ihre neue Anschrift und vermeiden Fehladressierungen.`}
                    </Typography>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        {`Die Umzugsmitteilung gilt automatisch für alle im Auftrag genannten Personen.`}
                    </Typography>
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCloseModal} style={{ margin: 4 }} autoFocus>
                    Schliessen
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default NoticeOfRelocateDialog;