import React, {useEffect, useState} from 'react';
import {Box, Container, Grid, makeStyles, Paper, Typography} from '@material-ui/core';
import {Elements} from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import { useSelector } from 'react-redux';
import Meta from '../components/Meta';
import '../assets/scss/stripe/_stripe_app.scss';
import {ORDER_INFO, STRIPE_TEST_PUBLIC_KEY} from "../constants/key.constant";
import Loader from '../components/Loader';
import CheckoutForm from "../components/Stripe/CheckoutForm";
import {COLOR_PRIMARY_DARK} from "../constants/color.constant";

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    boxContainer: {
        position: 'relative',
        ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
        padding: '24px 10%',
        height: '100%',
        [theme.breakpoints.down('xs')]: {
          padding: '24px 5%',
        },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
}));

const stripePromise = loadStripe(STRIPE_TEST_PUBLIC_KEY, {
  betas: ["process_order_beta_1"],
  apiVersion: "2020-08-27; orders_beta=v3"
});

const CheckoutScreen = ({ history }) => {
    const classes = useStyles();
    const [clientSecret, setClientSecret] = useState("");
    const { loading, error, isSuccess, orderData } = useSelector((state) => state.order);

    let localData = localStorage.getItem(ORDER_INFO);
    if (!localData) {
        history.push('/');
    } else {
        localData = JSON.parse(localData);
    }

    useEffect(() => {
        if (isSuccess && orderData) {
            setClientSecret(orderData.clientSecret);
        }
    }, [orderData, isSuccess, setClientSecret]);

    const appearance = {
        theme: 'stripe',
        variables: {
            colorPrimary: COLOR_PRIMARY_DARK
        }
    };

    const options = {
        clientSecret,
        appearance,
    };

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}></Grid>
            <Box className={classes.boxContainer}>
                { loading &&
                    <>
                        <Loader />
                        <Typography variant='h6'>Laden von Zahlungsinformationen...</Typography>
                    </>
                }
                { clientSecret &&
                    <Paper elevation={0} className={classes.content} square>
                        <Grid container spacing={8}>
                            <Grid item xs={12}>
                                    <Elements options={options} stripe={stripePromise}>
                                        <CheckoutForm />
                                    </Elements>
                            </Grid>
                        </Grid>
                    </Paper>
                }
            </Box>
        </Container>
        // <Container maxWidth='xl'>
        //     <Meta title='Nachsenden.info - Kasse 📯' />
        //     <Grid container className={classes.breadcrumbsContainer}></Grid>
        //     <Box className={classes.boxContainer}>
        //         <Paper elevation={0} className={classes.content} square>
        //             <Grid container spacing={8}>
        //                 <Grid item xs={12}>
        //                     <Box id='checkout-container'>
        //                       {/*{clientSecret && (*/}
        //                       {/*  <Elements options={options} stripe={stripePromise}>*/}
        //                       {/*    <CheckoutForm />*/}
        //                       {/*  </Elements>*/}
        //                       {/*)}*/}
        //                     </Box>
        //                 </Grid>
        //             </Grid>
        //         </Paper>
        //     </Box>
        // </Container>
    );
};

export default CheckoutScreen;