import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Typography } from '@material-ui/core';
import React from 'react';

const wrapWithBold = (props) => {
    return <span style={{fontWeight: "bold"}}>
        {props}
    </span>
}

const InsolvencyDialog = ({ openModal, handleCloseModal }) => {
    return (
        <Dialog
            open={openModal}
            onClose={handleCloseModal}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
                {"Insolvenz - Bitte beachten"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description" component={'div'}>
                    <Typography id="modal-modal-description" style={{ marginTop: 8 }} component='p'>
                        Bitte beachten Sie, dass Sie bei der Angabe der Anschrift den {wrapWithBold("insolventen Betrieb")} im dafür vorgesehenen Feld eintragen müssen. Bitte tragen Sie dort {wrapWithBold("nicht")} Ihre eigene Firma oder sich selbst ein!
                    </Typography>
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCloseModal} style={{ margin: 4 }} autoFocus>
                    Schliessen
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default InsolvencyDialog;