import React, { useEffect, useState } from 'react';
import {
    alpha,
    Box,
    Breadcrumbs,
    Button,
    Checkbox, Chip,
    Container,
    Divider,
    FormControl,
    FormControlLabel,
    FormHelperText,
    FormLabel,
    Grid, IconButton,
    InputBase,
    InputLabel,
    makeStyles, MenuItem,
    NativeSelect,
    Paper,
    Select,
    Typography, useTheme
} from '@material-ui/core';
import { Controller, FormProvider, useForm } from 'react-hook-form';
import { Link as RouterLink } from 'react-router-dom';
import { withStyles } from '@material-ui/styles';
import PhoneInput from 'react-phone-input-2';
import de from 'react-phone-input-2/lang/de.json';
import 'react-phone-input-2/lib/style.css';
import AddBoxIcon from '@material-ui/icons/AddBox';
import IndeterminateCheckBoxIcon from '@material-ui/icons/IndeterminateCheckBox';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import Meta from '../components/Meta';
import OrderSteps from '../components/OrderSteps';

import {
    CUSTOMER_TYPE_BUSINESS,
    CUSTOMER_TYPE_PRIVATE,
    ORDER_INFO,
    ORDER_REASON_CARE_CASE,
    ORDER_REASON_DEATH,
    ORDER_REASON_INSOLVENCY,
    ORDER_REASON_MOVE,
    SALUTATION_DIVERS,
    SALUTATION_MR,
    SALUTATION_MRS,
    OTHER_SERVICES
} from '../constants/key.constant';
import { COLOR_PRIMARY, COLOR_PRIMARY_DARK, COLOR_WHITE } from '../constants/color.constant';
import { Link } from 'react-router-dom';
import HelpPanel from '../components/HelpPanel';
import ParcelsDialog from '../components/dialog/ParcelsDialog';
import InfopostDialog from '../components/dialog/InfopostDialog';
import {get} from "../utils";
import NoticeOfRelocateDialog from "../components/dialog/NoticeOfRelocate";

const useStyles = makeStyles((theme) => ({
    breadcrumbsContainer: {
      ...theme.mixins.customize.breadcrumbs,
    },
    form: {
      '& > *': {
        marginBottom: 16,
      },
    },
    boxContainer: {
      position: 'relative',
      ...theme.mixins.customize.flexMixin('center', 'center', 'column'),
      padding: '24px',
      height: '100%',
      [theme.breakpoints.down('xs')]: {
        padding: '24px',
      },
    },
    content: {
      padding: 24,
      boxShadow: '0 10px 31px 0 rgba(0,0,0,0.05)',
    },
    chips: {
        display: 'flex',
        flexWrap: 'wrap'
    },
    chip: {
        margin: 2,
        backgroundColor: COLOR_WHITE,
        color: COLOR_PRIMARY_DARK,
    },
    multiClear: {
        color: COLOR_PRIMARY_DARK,
        marginTop: 16
    }
}));

const Input = withStyles((theme) => ({
    root: {
        'label + &': {
            marginTop: theme.spacing(3),
        },
        '&.Mui-error $input': {
            borderColor: '#f44336',
        },
    },
    input: {
        borderRadius: 4,
        position: 'relative',
        backgroundColor: theme.palette.common.white,
        border: '1px solid #ced4da',
        fontSize: 16,
        padding: '10px 12px',
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        '&:focus': {
            boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
            borderColor: theme.palette.primary.main,
        },
    },
}))(InputBase);

const PrimaryCheckBox = withStyles({
    root: {
      color: COLOR_PRIMARY,
      '&$checked': {
        color: COLOR_PRIMARY_DARK,
      },
    },
    checked: {},
  })((props) => <Checkbox color="default" {...props} />);

const getMultipleSelectStyles = (name, allNames, theme) => {
    return {
        fontWeight:
            allNames.indexOf(name) === -1
                ? theme.typography.fontWeightRegular
                : theme.typography.fontWeightMedium,
    };
}

const ReceiverScreen = ({ history }) => {
    const classes = useStyles();
    const theme = useTheme();
    const methods = useForm({ shouldUnregister: true });
    const { handleSubmit, control, setValue } = methods;
    const [ focused, setFocused ] = useState('');

    const [openParcelsModal, setParcelsModal] = useState(false);
    const [openInfopostModal, setInfopostModal] = useState(false);
    const [openNoticeOfRelocation, setNoticeOfRelocation] = useState(false);

    const handleOpenParcelsModal = () => setParcelsModal(true);
    const handleAcceptParcelsModal = () => {
        setParcelsModal(false);
        setValue('parcel_shipments', true);
        setParcelAccepted(true);
    }
    const handleRejectParcelsModal = () => {
        setParcelsModal(false);
        setValue('parcel_shipments', false);
        setParcelAccepted(false);
    }
    const handleOpenInfopostModal = () => setInfopostModal(true);
    const handleCloseInfopostModal = () => setInfopostModal(false);
    const handleOpenNoticeOfRelocation = () => setNoticeOfRelocation(true);
    const handleCloseNoticeOfRelocation = () => setNoticeOfRelocation(false);

    // Local Storage Data
    let localData = localStorage.getItem(ORDER_INFO);
    let salutationDefault = SALUTATION_MR;
    let representativeSalutationDefault = SALUTATION_MR;
    let nameFirstDefault = '';
    let nameLastDefault = '';
    let representativeNameFirstDefault = '';
    let representativeNameLastDefault = '';
    let emailDefault = '';
    let phoneNumberDefault = '';
    let companyNameDefault = '';
    let noticeOfRelocationDefault = false;
    let parcelShipmentsDefault = false;
    let dhlINFOPOSTDefault = false;
    let optionalNameModeDefault = false;
    let optionalNamesIdxDefault = 0;
    let optionalNamesDefault = [];
    let additionalServicies = [];
    // console.log('load ', localData);
    if (localData) {
        localData = JSON.parse(localData);
        if (!localData.customer_type) {
            history.push('/');
        }
        if (localData.salutation) {
            salutationDefault = localData.salutation;
        }
        if (localData.representativeSalutation) {
            representativeSalutationDefault = localData.representativeSalutation;
        }
        if (localData.nameFirst) {
            nameFirstDefault = localData.nameFirst;
        }
        if (localData.nameLast) {
            nameLastDefault = localData.nameLast;
        }
        if (localData.representativeNameFirst) {
            representativeNameFirstDefault = localData.representativeNameFirst;
        }
        if (localData.representativeNameLast) {
            representativeNameLastDefault = localData.representativeNameLast;
        }
        if (localData.email) {
            emailDefault = localData.email;
        }
        if (localData.phone_number) {
            phoneNumberDefault = localData.phone_number;
        }
        if (localData.notice_of_relocation) {
            noticeOfRelocationDefault = localData.notice_of_relocation;
        }
        if (localData.parcel_shipments) {
            parcelShipmentsDefault = localData.parcel_shipments;
        }
        if (localData.dhl_infopost) {
            dhlINFOPOSTDefault = localData.dhl_infopost;
        }
        if (localData.company_name) {
            companyNameDefault = localData.company_name;
        }
        if (localData.other_service && localData.other_service.length > 0) {
            localData.other_service.forEach(name => additionalServicies.push(name));
        }
        if (localData.additional_recipients && localData.additional_recipients.length > 0) {
            optionalNameModeDefault = true;
            optionalNamesDefault = localData.additional_recipients;
            optionalNamesIdxDefault = optionalNamesDefault[optionalNamesDefault.length - 1].idx;
            // console.log(optionalNamesDefault);
        }
    } else {
        history.push('/');
    }

    const [otherServices, setOtherServices] = useState(additionalServicies);
    const [noticeOfRelocatioin, setNoticeOfRelocatioin] = useState(noticeOfRelocationDefault);
    const [parcelAccepted, setParcelAccepted] = useState(parcelShipmentsDefault);
    const [dhlInfopost, setDhlINFOPOST] = useState(dhlINFOPOSTDefault);

    const [optionalNameMode, setOptionalNameMode] = useState(optionalNameModeDefault);
    const [optionalIdx, setOptionalIdx] = useState(optionalNamesIdxDefault);
    const [optionalNames, setOptionalNames] = useState(optionalNamesDefault);

    useEffect(() => {
        // console.log('change ', optionalNames);
        if (optionalNames.length === 0) {
            setOptionalNameMode(false);
            setOptionalIdx(0);
            // setOptionalNames([{ idx:0, first: '', last: '' }]);
        }
    }, [optionalNames]);

    const handleOptionalFirstChange = (e, key) => {
        const arr = [...optionalNames];
        arr[key].first = e.target.value;
        setValue(`nameFirst-opt${key}`, arr[key].first);
        setOptionalNames(arr);
    }

    const handleOptionalLastChange = (e, key) => {
        const arr = [...optionalNames];
        arr[key].last = e.target.value;
        setValue(`nameLast-opt${key}`, arr[key].last);
        setOptionalNames(arr);
    }

    const handleParcelsCheckBox = (e) => {
        if (e.target.checked) {
            handleOpenParcelsModal();
        } else {
            setParcelAccepted(false);
        }
    }

    const handleTitleText = () => {
        // if (localData) {
        //     if (localData.customer_type === CUSTOMER_TYPE_BUSINESS) {
        //         if (localData.customer_type === ORDER_REASON_INSOLVENCY) {
        //             return 'Insolvente Firma angeben';
        //         }
        //         return 'Firma / Betrieb angeben';
        //     }
        //     if (localData.customer_type === ORDER_REASON_DEATH) {
        //         return 'Bisherigen Persönliche Daten angeben';
        //     }
        // }
        return 'Art & Startzeitpunkt';
    }

    const onNext = (data) => {
        localData = { ...localData, ...data};
        localData.additional_recipients = [...optionalNames];
        localData.other_price = 0.00;
        localData.other_service = otherServices;
        if (otherServices.length > 0) {

            otherServices.map((name) => {
                localData.other_price += get(OTHER_SERVICES.find(service => service.name === name), localData.customer_type === CUSTOMER_TYPE_PRIVATE ? 'private' : 'business', 0);
                return name;
            });
        }

        // Preparing Data for Server
        localData.full_name = `${data.salutation} ${data.nameFirst} ${data.nameLast}`;
        if (data.representativeNameFirst) {
            localData.representative_full_name = `${data.representativeSalutation} ${data.representativeNameFirst} ${data.representativeNameLast}`;
        }

        console.log(localData);
        localStorage.setItem(ORDER_INFO, JSON.stringify(localData));
        history.push('/address');
    };

    return (
        <Container maxWidth='xl'>
            <Meta title='mein-nachsendeservice.de - Ihr Nachsendeservice aus Deutschland. Wählen Sie bis zu 16 Versanddienstleister aus' />
            <Grid container className={classes.breadcrumbsContainer}>
                <Grid item xs={12}>
                    <Breadcrumbs style={{ marginBottom: 24 }}/>
                    <OrderSteps step={1} />
                </Grid>
            </Grid>
            <Box className={classes.boxContainer}>
                <Paper elevation={0} className={classes.content} square>
                    <Grid container spacing={8}>
                        <Grid item sm={12} md={7}>
                            <Typography variant='h4' gutterBottom>
                                {handleTitleText()}
                            </Typography>
                            <Divider style={{ marginTop:16, marginBottom: 16 }} />

                            <FormProvider {...methods}>
                                <form className={classes.form} onSubmit={handleSubmit(onNext)}>
                                    <Grid container spacing={2}>
                                        { localData && localData.customer_type === CUSTOMER_TYPE_BUSINESS &&
                                            <><Grid item xs={12}>
                                            <Controller
                                                name='company_name'
                                                control={control}
                                                defaultValue={companyNameDefault}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='receiver-companyname'>
                                                            Firmenname: *
                                                        </InputLabel>
                                                        <Input {...field} id='receiver-companyname' placeholder='Name Ihrer Firma' onFocus={() => setFocused('company')}  />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{
                                                    required: 'Dieses Feld wird benötigt.',
                                                }}
                                                />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <Divider style={{marginTop: 2, marginBottom: 4}} />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <Typography variant='h6' >
                                                    Bevollmächtigter / Ansprechpartner
                                                </Typography>
                                            </Grid> </>}


                                            <Grid item xs={12} sm={6}>
                                                <Controller
                                                    name='salutation'
                                                    control={control}
                                                    defaultValue={salutationDefault}
                                                    render={({ field, fieldState: { error } }) => (
                                                <FormControl fullWidth error={!!error}>
                                                    <InputLabel shrink htmlFor='receiver-salutation'>
                                                        Anrede:
                                                    </InputLabel>
                                                    <NativeSelect
                                                        {...field}
                                                        id="receiver-salutation"
                                                        name='salutation'
                                                        style={{
                                                            minHeight: "48px"
                                                        }}
                                                        onFocus={() => setFocused(localData.customer_type === CUSTOMER_TYPE_PRIVATE ? 'salutation' : 'representativeSalutation')}
                                                        inputProps={{ 'aria-label': 'salutation' }}>
                                                        <option value={SALUTATION_MR}>{SALUTATION_MR}</option>
                                                        <option value={SALUTATION_MRS}>{SALUTATION_MRS}</option>
                                                        <option value={SALUTATION_DIVERS}>{SALUTATION_DIVERS}</option>
                                                    </NativeSelect>
                                                </FormControl>
                                                )}/>
                                        </Grid>
                                        <Grid item xs={12} sm={6}></Grid>
                                        <Grid item xs={12}>
                                            <Divider style={{marginTop: 2, marginBottom: 2}} />
                                        </Grid>

                                        <Grid item xs={12} sm={6}>
                                            <Controller
                                                name='nameFirst'
                                                control={control}
                                                defaultValue={nameFirstDefault}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='receiver-namefirst'>
                                                            Vorname: *
                                                        </InputLabel>
                                                        <Input {...field} id='receiver-namefirst' placeholder='Vorname' onFocus={() => setFocused(localData.customer_type === CUSTOMER_TYPE_PRIVATE ? 'name' : 'representativeName')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{
                                                  required: 'Dieses Feld wird benötigt.',
                                                }}
                                                />
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <Controller
                                                name='nameLast'
                                                control={control}
                                                defaultValue={nameLastDefault}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='receiver-namelast'>
                                                            Nachname: *
                                                        </InputLabel>
                                                        <Input {...field} id='receiver-namelast' placeholder='Nachname' onFocus={() => setFocused(localData.customer_type === CUSTOMER_TYPE_PRIVATE ? 'name' : 'representativeName')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{
                                                  required: 'Dieses Feld wird benötigt.',
                                                }}
                                                />
                                        </Grid>

                                        {localData && localData.customer_type === CUSTOMER_TYPE_PRIVATE && !optionalNameMode && <Grid item xs={12} style={{ marginTop: -8, marginBottom: -8 }}>
                                            <Box display='flex' justifyContent={"flex-end"}>
                                                    <div style={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                        flexWrap: 'wrap',
                                                        cursor: 'pointer',
                                                        color: COLOR_PRIMARY_DARK
                                                    }} onClick={() => {
                                                        setOptionalNameMode(true);
                                                        setOptionalIdx(0);
                                                        setOptionalNames([{ idx: 0, first: '', last: '' }]);
                                                    }}>
                                                        <AddBoxIcon /><span>{' Empfänger hinzufügen (optional)'}</span>
                                                    </div>
                                            </Box>
                                        </Grid>}

                                        {optionalNameMode && <>
                                            <Grid item xs={12} sm={12}>
                                                <Divider style={{marginTop: 2, marginBottom: 2}} />
                                            </Grid>
                                            <Typography variant='h6' gutterBottom>
                                                Weitere Empfänger (optional)
                                            </Typography>

                                            {optionalNames.map((name, key) => (
                                                <Grid container spacing={2} key={name.idx} style={{ paddingTop: 8 }}>
                                                    <Grid item xs={12} sm={5}>
                                                        <Controller
                                                            name={`nameFirst-opt${name.idx}`}
                                                            control={control}
                                                            defaultValue={name.first}
                                                            render={({ field: {value}, fieldState: { error } }) => (
                                                                <FormControl fullWidth error={!!error}>
                                                                    <Input id={'receiver-namefirst-opt'+name.idx} value={value} placeholder='Vorname *' onChange={(e) => handleOptionalFirstChange(e, name.idx)} onFocus={() => setFocused('anotherPerson')}  />{' '}
                                                                    {error && (
                                                                        <FormHelperText error>{error.message}</FormHelperText>
                                                                    )}
                                                                </FormControl>
                                                            )}
                                                            rules={{
                                                            required: 'Dieses Feld wird benötigt.',
                                                            }}
                                                            />
                                                    </Grid>
                                                    <Grid item xs={12} sm={5}>
                                                        <Controller
                                                            name={`nameLast-opt${name.idx}`}
                                                            control={control}
                                                            defaultValue={name.last}
                                                            render={({ field: {value}, fieldState: { error } }) => (
                                                                <FormControl fullWidth error={!!error}>
                                                                    <Input id={'receiver-namelast-opt'+name.idx} value={value} placeholder='Nachname *' onChange={(e) => handleOptionalLastChange(e, name.idx)} onFocus={() => setFocused('anotherPerson')}  />
                                                                    {error && (
                                                                        <FormHelperText error>{error.message}</FormHelperText>
                                                                    )}
                                                                </FormControl>
                                                            )}
                                                            rules={{
                                                            required: 'Dieses Feld wird benötigt.',
                                                            }}
                                                            />
                                                    </Grid>
                                                    <Grid item xs={12} sm={2} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                                        <Box>
                                                            <div style={{
                                                                display: 'flex',
                                                                alignItems: 'center',
                                                                flexWrap: 'wrap',
                                                                cursor: 'pointer',
                                                                color: 'red'
                                                            }} onClick={() => {
                                                                const arr = [...optionalNames];
                                                                // console.log('before ', arr, key);
                                                                arr.splice(key, 1);
                                                                // console.log('after ', arr);
                                                                setOptionalNames(arr);
                                                            }}>
                                                            <IndeterminateCheckBoxIcon /><span>{' löschen'}</span>
                                                            </div>

                                                        </Box>
                                                    </Grid>
                                                </Grid>
                                            ))}

                                            <Grid item xs={12}>
                                                <Box display='flex' justifyContent={"flex-end"}>
                                                        <div style={{
                                                            display: 'flex',
                                                            alignItems: 'center',
                                                            flexWrap: 'wrap',
                                                            cursor: 'pointer',
                                                            color: COLOR_PRIMARY_DARK
                                                        }} onClick={() => {
                                                            let value = parseInt(optionalIdx) + 1;
                                                            const arr = [...optionalNames, { idx: value, first: '', last: '' }];
                                                            setOptionalIdx(value);
                                                            setOptionalNames(arr);
                                                        }}>
                                                            <AddBoxIcon /><span>{' weitere Empfänger hinzufügen (optional)'}</span>
                                                        </div>
                                                </Box>
                                            </Grid>

                                            <Grid item xs={12} sm={12}>
                                                <Divider style={{marginTop: 2, marginBottom: 2}} />
                                            </Grid>
                                        </>}

                                        <Grid item xs={12}>
                                            <Controller
                                                name='email'
                                                control={control}
                                                defaultValue={emailDefault}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='receiver-email'>
                                                            E-Mail: *
                                                        </InputLabel>
                                                        <Input {...field} id='receiver-email' placeholder='E-Mail Adresse' onFocus={() => setFocused('email')}  fullWidth />{' '}
                                                        {error && (
                                                        <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{
                                                  required: 'Dieses Feld wird benötigt.',
                                                  pattern: {
                                                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                                    message: 'Ungültige E-Mail Adresse',
                                                  },
                                                }}
                                                />
                                        </Grid>
                                        {/* <Grid item xs={12}>
                                            <Divider style={{marginTop: 2, marginBottom: 2}} />
                                        </Grid> */}

                                        <Grid item xs={12}>
                                            <Controller
                                                name='phone_number'
                                                control={control}
                                                defaultValue={phoneNumberDefault}
                                                render={({ field: { onChange, value }, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='receiver-phonenumber'>
                                                            Tel.-Nr. (inkl. Vorwahl):
                                                        </InputLabel>
                                                        <PhoneInput
                                                            id="receiver-phonenumber"
                                                            placeholder="Tel.-Nr. (inkl. Vorwahl) z.B. +49 30 12345678"
                                                            country={'de'}
                                                            localization={de}
                                                            style={{
                                                              marginTop: "24px",
                                                            }}
                                                            inputStyle={{
                                                              width: "100%",
                                                              height: "42px"
                                                            }}
                                                            onChange={(value, country, e, formattedValue) => {
                                                                onChange(formattedValue)
                                                            }}
                                                            onFocus={() => setFocused(localData.customer_type === CUSTOMER_TYPE_PRIVATE ? 'phoneNumber' : 'representativePhoneNumber')}
                                                            value={value}
                                                            fullWidth/>
                                                            {error && (
                                                                <FormHelperText error>{error.message}</FormHelperText>
                                                            )}
                                                    </FormControl>
                                                )} />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <Divider style={{marginTop: 2, marginBottom: 2}} />
                                        </Grid>

                                        { localData && (localData.order_reason === ORDER_REASON_CARE_CASE || localData.order_reason === ORDER_REASON_DEATH) && <>
                                        <Grid item xs={12}>
                                            <Typography variant='h6' gutterBottom>
                                                Bevollmächtigter
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                        <Controller
                                            name='representativeSalutation'
                                            control={control}
                                            defaultValue={representativeSalutationDefault}
                                            render={({ field, fieldState: { error } }) => (
                                            <FormControl fullWidth error={!!error}>
                                                <InputLabel shrink htmlFor='receiver-representativesalutation'>
                                                    Anrede:
                                                </InputLabel>
                                                <NativeSelect
                                                    {...field}
                                                    id="receiver-representativesalutation"
                                                    name='salutation'
                                                    style={{
                                                        minHeight: "48px"
                                                    }}
                                                    onFocus={() => setFocused('representativeSalutation')}
                                                    inputProps={{ 'aria-label': 'salutation' }}>
                                                    <option value={SALUTATION_MR}>{SALUTATION_MR}</option>
                                                    <option value={SALUTATION_MRS}>{SALUTATION_MRS}</option>
                                                    <option value={SALUTATION_DIVERS}>{SALUTATION_DIVERS}</option>
                                                </NativeSelect>
                                            </FormControl>
                                            )}
                                        />
                                        </Grid>
                                        <Grid item xs={12} sm={6}></Grid>

                                        <Grid item xs={12} sm={6}>
                                            <Controller
                                                name='representativeNameFirst'
                                                control={control}
                                                defaultValue={representativeNameFirstDefault}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='receiver-representativenamefirst'>
                                                            Vorname: *
                                                        </InputLabel>
                                                        <Input {...field} id='receiver-representativenamefirst' placeholder='Vorname' onFocus={() => setFocused('representativeName')}  />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{
                                                  required: 'Dieses Feld wird benötigt.',
                                                }}
                                                />
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <Controller
                                                name='representativeNameLast'
                                                control={control}
                                                defaultValue={representativeNameLastDefault}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <InputLabel shrink htmlFor='receiver-representativenamelast'>
                                                            Nachname: *
                                                        </InputLabel>
                                                        <Input {...field} id='receiver-representativenamelast' placeholder='Nachname' onFocus={() => setFocused('representativeName')} />{' '}
                                                        {error && (
                                                            <FormHelperText error>{error.message}</FormHelperText>
                                                        )}
                                                    </FormControl>
                                                )}
                                                rules={{
                                                  required: 'Dieses Feld wird benötigt.',
                                                }}
                                                />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <Divider style={{marginTop: 2, marginBottom: 8}} />
                                        </Grid>
                                        </> }

                                        <Grid item xs={12}>
                                            <Typography variant='h6' gutterBottom>
                                                Optionale Zusatzleistungen
                                            </Typography>
                                        </Grid>

                                        {localData && localData.order_reason === ORDER_REASON_MOVE && <Grid item xs={12}>
                                            <Controller
                                                name='notice_of_relocation'
                                                control={control}
                                                defaultValue={noticeOfRelocationDefault}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <FormLabel>Umzugsmitteilung:</FormLabel>
                                                        <FormControlLabel
                                                            checked={value}
                                                            onChange={e => { setNoticeOfRelocatioin(e.target.checked); onChange(e); }}
                                                            onFocus={() => setFocused('noticeOfRelocation')}
                                                            control={<PrimaryCheckBox />}
                                                            label={<label>Ja, ich möchte die kostenlose <Link to='#' onClick={e => { e.preventDefault();  handleOpenNoticeOfRelocation(); }}>Umzugsmitteilung</Link></label>}
                                                        />
                                                    </FormControl>
                                                )} />
                                        </Grid> }
                                        <Grid item xs={12} sm={6}>
                                            <Controller
                                                name='parcel_shipments'
                                                control={control}
                                                defaultValue={parcelShipmentsDefault}
                                                render={({ field, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <FormLabel>Paketsendungen:</FormLabel>
                                                        <FormControlLabel
                                                            {...field}
                                                            checked={parcelAccepted}
                                                            onChange={handleParcelsCheckBox}
                                                            onFocus={() => setFocused('parcel_shipments')}
                                                            control={<PrimaryCheckBox />}
                                                            label={'Pakete und Päckchen nachsenden'}
                                                        />
                                                    </FormControl>
                                                )} />
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <Controller
                                                name='dhl_infopost'
                                                control={control}
                                                defaultValue={dhlINFOPOSTDefault}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <FormControl fullWidth error={!!error}>
                                                        <FormLabel>DHL Infopost:</FormLabel>
                                                        <FormControlLabel
                                                            checked={value}
                                                            onChange={e => { setDhlINFOPOST(e.target.checked); onChange(e); }}
                                                            control={<PrimaryCheckBox />}
                                                            label={<label><Link to='#' onClick={e => { e.preventDefault();  handleOpenInfopostModal(); }}>weitere Informationen</Link></label>}
                                                        />
                                                    </FormControl>
                                                )} />
                                        </Grid>
                                        <Grid item xs={12} sm={11}>
                                            <Controller
                                                name='other_service'
                                                control={control}
                                                defaultValue={localData ? get(localData, 'other_service', '') : ''}
                                                render={({ field, fieldState: { error } }) => (
                                            <FormControl fullWidth error={!!error}>
                                                <InputLabel shrink htmlFor='receiver-other-service'>
                                                    Weitere Versanddienstleister:
                                                </InputLabel>
                                                <Select
                                                    {...field}
                                                    multiple
                                                    id="receiver-other-service"
                                                    name='other_service'
                                                    inputProps={{ 'aria-label': 'other_service' }}
                                                    value={otherServices}
                                                    onChange={evt => setOtherServices(evt.target.value)}
                                                    input={<Input id={"select-multiple-chip"} />}
                                                    renderValue={(selected) => (
                                                        <div className={classes.chips}>
                                                            {
                                                                selected.map((value) => (
                                                                    <Chip key={value} label={value} variant='outlined' className={classes.chip} />
                                                                ))
                                                            }
                                                        </div>
                                                    )}
                                                >
                                                    {OTHER_SERVICES.map((service, idx) => (
                                                        <MenuItem key={idx} value={service.name} style={getMultipleSelectStyles(service.name, otherServices, theme)}>
                                                            {service.name}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                            )}/>
                                        </Grid>
                                        <Grid item xs={12} sm={1}>
                                            <IconButton className={classes.multiClear} onClick={() => {setOtherServices([])}}>
                                                <HighlightOffIcon />
                                            </IconButton>
                                        </Grid>
                                        <Grid item xs={12}></Grid>
                                        <Grid item xs={12} sm={12}>
                                            <Box display='flex' justifyContent={"space-between"} pt={1}>
                                                <Button
                                                    variant='contained'
                                                    component={RouterLink}
                                                    to='/'
                                                    style={{ marginLeft: 8 }}>
                                                    zurück
                                                </Button>
                                                <Button
                                                    type='submit'
                                                    variant='contained'
                                                    style={{
                                                        color: COLOR_WHITE,
                                                        backgroundColor: COLOR_PRIMARY_DARK
                                                    }}>
                                                    weiter
                                                </Button>
                                            </Box>
                                        </Grid>
                                    </Grid>
                                </form>
                            </FormProvider>
                        </Grid>
                        <Grid item sm={12} md={5} style={{borderLeft: '1px solid lightgray', marginTop: 32, marginBottom: 32}}>
                            <Box sx={{
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                width: '100%',
                                height: '100%',
                                flexDirection: 'column'
                            }}>
                                <HelpPanel field={focused} />
                            </Box>
                        </Grid>
                    </Grid>
                </Paper>
            </Box>
            <ParcelsDialog
                openModal={openParcelsModal}
                handleAcceptModal={handleAcceptParcelsModal}
                handleRejectModal={handleRejectParcelsModal} />
            <InfopostDialog
                openModal={openInfopostModal}
                handleCloseModal={handleCloseInfopostModal} />
            <NoticeOfRelocateDialog
                openModal={openNoticeOfRelocation}
                handleCloseModal={handleCloseNoticeOfRelocation} />
        </Container>
    );
};

export default ReceiverScreen;