import React, {useState} from 'react';
import {Button} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import PaypalImage from '../assets/images/payap1.png';
import StripeImage from '../assets/images/stripe.png';
import {COLOR_GRAY_1, COLOR_PRIMARY_DARK} from "../constants/color.constant";
import {PAYMENT_SERVICE_PAYPAL, PAYMENT_SERVICE_STRIPE} from "../constants/key.constant";

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        '& > *': {
          margin: theme.spacing(1),
        },
    },
    logoButton: {
        width: '30%',
        height: '48px',
        border: '1px solid',
        borderColor: COLOR_GRAY_1
    },
    logoButtonSelected: {
        width: '30%',
        height: '48px',
        border: '3px solid',
        borderColor: COLOR_PRIMARY_DARK
    },
    logoPaypal: {
        width: '100%',
        height: '100%',
        maxWidth: '75%',
        maxHeight: '48px'
    },
    logoStripe: {
        width: '100%',
        height: '100%',
        maxWidth: '60%',
        maxHeight: '48px'
    }
}));

const PaymentGateway = ({ paymentService, setPaymentService, setFocused }) => {
    const classes = useStyles();
    const [method, setMethod] = useState(paymentService);
    const handleChange = (e, newMethod) => {
        setMethod(newMethod);
        setPaymentService(newMethod);
        setFocused('paymentService');
    };

    return (
        <div className={classes.root}>
            <Button className={method === PAYMENT_SERVICE_PAYPAL ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_PAYPAL)}>
                <img src={PaypalImage} className={classes.logoPaypal} alt='paypal' />
            </Button>
            <Button className={method === PAYMENT_SERVICE_STRIPE ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_STRIPE)}>
                <img src={StripeImage} className={classes.logoStripe} alt='stripe' />
            </Button>
        </div>
    );
};

export default PaymentGateway;