import React from 'react';
import { Link, makeStyles, Step, StepLabel, Stepper } from '@material-ui/core';
import { COLOR_PRIMARY_DARK } from '../constants/color.constant';
import { Link as RouterLink } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%'
    },
    stepper: {
        backgroundColor: theme.palette.background.default,
        padding: 0,
        '& .MuiStepIcon-completed, .MuiStepIcon-active': {
          color: COLOR_PRIMARY_DARK
        },
    }
}));

const OrderSteps = ({ step }) => {
    const classes = useStyles();
    const steps = ['Art & Startzeitpunkt', 'Daten Empfänger', 'Anschriften', 'Übersicht', 'Zusammenfassung'];
    const links = ['/', '/receiver', '/address', '/summary', '/payment'];

    return (
        <div className={classes.root}>
            <Stepper activeStep={step} alternativeLabel className={classes.stepper}>
                {steps.map((label, key) => (
                    <Step key={label}>
                        <StepLabel>
                            { key < step ? (
                                <Link component={RouterLink} to={links[key]} color='textPrimary'>
                                    {label}
                                </Link>
                            ) : (
                                <>
                                {label}
                                </>
                            ) }
                        </StepLabel>
                    </Step>
                ))}
            </Stepper>
        </div>
    );
};

export default OrderSteps;